<template>
  <div class="translate" v-if="current">
    <top-info
      title="翻译练习"
      :currentIndex="currentIndex"
      :len="len"
      :studyMode="studyMode"
      @toggleStudyMode="studyMode = !studyMode"
    />
    <p v-if="showLastIndex">上次学到第 <span class="blue">{{lastIndex + 1}}</span> 题
    <span class="blue" @click="proceed" type="text" v-if="lastIndex">继续学习 <i class="el-icon-arrow-right el-icon--right"></i></span></p>
    <p>{{studyMode ? current['eng-help'] : current.eng}}</p>
    <p class="green" v-if="percent && percent > 0.6">匹配度：{{ this.percent | showPercent }}</p>
    <p class="b" v-if="studyMode">{{current['zh-cn']}}</p>
    <el-input v-if="!studyMode" @blur="handleBlur" class="input" placeholder="输入翻译内容" type="textarea" rows="2" v-model="currentVal"/>
    <controller
      @changeIndex="changeIndex" 
      :currentIndex="currentIndex"
      :studyMode="studyMode"
      :Ans="current['zh-cn']"
      :len="len"
    >
      <template v-slot:head>
        <el-button size="small" type="primary" @click="handleCollect">{{ getCollectStr }}</el-button>
        <p>收藏的题：</p>
        <span class="blue" v-for="item in collect" :key="item" @click="changeIndex(item)">
          第{{ item + 1 }}题 
        </span>
      </template>
    </controller>
  </div>
</template>

<script>
import strSimilarity2Percent from '@/util'
import Controller from '@/components/Controller'
import TopInfo from '@/components/TopInfo'
import translate from '@/data/translate'

export default {
  components: { Controller, TopInfo },
  data () {
    return {
      currentIndex: 0,
      currentVal: null,
      percent: null,
      len: null,
      lastIndex: null,
      studyMode: false,
      collect: []
    }
  },

  methods: {
    changeIndex (target) {
      this.currentIndex = target
    },
    proceed () {
      this.currentIndex = this.lastIndex
    },
    handleBlur () {
      if (this.percent >= .8) {
        this.$message({
          duration: 1000,
          type: 'success',
          message: '回答正确，自动进入下一题',
          offset: 0
        })
        this.currentIndex++
      }
    },
    handleCollect () {
      const collectIndex = this.collect.indexOf(this.currentIndex)
      if (collectIndex > -1) {
        this.collect.splice(collectIndex, 1)
        localStorage.setItem(`${this.title}collect`, this.collect)
      } else {
        if (!this.collect.includes(this.currentIndex)) {
          this.collect.push(this.currentIndex)
          localStorage.setItem(`${this.title}collect`, this.collect)
        }
      }
    }
  },

  filters: {
    showPercent (val) {
      return Number(val * 100).toFixed(2) + '%'
    }
  },

  computed: {
    current () {
      return translate.list[this.currentIndex]
    },
    showLastIndex () {
      return this.lastIndex && (this.currentIndex !== this.lastIndex)
    },

    getCollectStr () {
      const collectIndex = this.collect.indexOf(this.currentIndex)
      if (collectIndex > -1) {
        return '取消收藏'
      } else {
        return '收藏'
      }
    }
  },

  watch: {
    currentVal (val) {
      if (!val) return
      this.percent = strSimilarity2Percent(val, this.current['zh-cn'])
      if (val[val.length - 1] === '\n') {
        this.handleBlur()
      }
    },
    currentIndex () {
      this.lastIndex = null
      this.currentVal = null
      this.percent = null
      localStorage.setItem('翻译练习lastIndex', this.currentIndex)
    }
  },

  mounted () {
    this.len = translate.list.length
    const lastIndex = localStorage.getItem('翻译练习lastIndex') || localStorage.getItem('lastIndex')
    if (lastIndex) {
      this.lastIndex = parseInt(lastIndex)
    }

    const collect = localStorage.getItem(`${this.title}collect`)
    this.collect = collect.split(',').map(n => parseInt(n)).filter(n => Number.isInteger(n))
  }
}
</script>

