const list = [{"eng-help":"The People's Republic of China (PRC), founded（成立）on October 1, 1949, covers an area（覆盖面积）of 9.6 million square kilometers.","eng":"The People's Republic of China (PRC), founded on October 1, 1949, covers an area of 9.6 million square kilometers.","zh-cn":"中华人民共和国成立于1949年10月1日，国土面积约960万平方公里。"},{"eng-help":"This new country hopes to establish friendly relations（建立友好关系） with all its neighbours.","eng":"This new country hopes to establish friendly relations with all its neighbours.","zh-cn":"这个新成立的国家希望和所有邻国建立友好关系。"},{"eng-help":"It’s very important to maintain（保持）your current weight（目前的体重） through exercise and healthy eating.","eng":"It’s very important to maintain your current weight through exercise and healthy eating.","zh-cn":"通过锻炼和健康饮食来保持你目前的体重是非常重要的。"},{"eng-help":"Most students feel satisfied with（对… 感到满意） the progress（进步） they’ve made.","eng":"Most students feel satisfied with the progress they’ve made.","zh-cn":"大多数学生对自己所取得的进步感到满意。"},{"eng-help":"Do you have access to（能使用） the Internet?","eng":"Do you have access to the Internet?","zh-cn":"你能上网吗？"},{"eng-help":"I knocked （敲） on his door but nobody came to answer （开门）it.","eng":"I knocked on his door but nobody came to answer it.","zh-cn":"我敲了他的门，但没人来开门。"},{"eng-help":"I rang（打电话） your house last night but your mother answered the phone.（接电话）","eng":"I rang your house last night but your mother answered the phone.","zh-cn":"我昨夜给你家打电话，但接电话的是你母亲。"},{"eng-help":"I would appreciate（感激）it if you would just let me deal with（处理） this case（事件，案件）.","eng":"I would appreciate it if you would just let me deal with this case.","zh-cn":"如果你能让我独自处理这桩案件，我将不胜感激。"},{"eng-help":"It normally（一般地） takes（花费） a semester（学期） for a college freshman（新生） to adjust（适应） to his college life.","eng":"It normally takes a semester for a college freshman to adjust to his college life.","zh-cn":"大学新生一般需要花一个学期来适应大学生活。"},{"eng-help":"Look out （向外看） the window it's still raining today!","eng":"Look out the window it's still raining today!","zh-cn":"看窗外，今天还在下雨。"},{"eng-help":"People all over the world（全世界） are trying to help the people in the quake-stricken areas（地震灾区）.","eng":"People all over the world are trying to help the people in the quake-stricken areas.","zh-cn":"全世界人民都在尽力帮助遭受地震地区的人民。"},{"eng-help":"The doctor had no choice but（只能） reach out to（求助于） their colleagues across the nation（全国）.","eng":"The doctor had no choice but reach out to their colleagues across the nation.","zh-cn":"那个医生别无选择，只能向全国的同行求助。"},{"eng-help":"The Olympic Games（奥运会）is an international sports event that takes place（举办） every four years.","eng":"The Olympic Games is an international sports event that takes place every four years.","zh-cn":"奥林匹克运动会是国际性的体育会，每四年举办一次。"},{"eng-help":"The traffic jams（交通拥堵） during morning and afternoon rush hours（高峰期） are a headache（头疼，难题） in big cities now.","eng":"The traffic jams during morning and afternoon rush hours are a headache in big cities now.","zh-cn":"上下班高峰期的交通拥堵问题是大城市目前的一个棘手问题。"},{"eng-help":"A friend of mine from high school is working in England now.","eng":"A friend of mine from high school is working in England now.","zh-cn":"我高中的一个朋友目前在英格兰工作。"},{"eng-help":"Are you fond of（喜爱） music?","eng":"Are you fond of music?","zh-cn":"你喜欢音乐吗？"},{"eng-help":"Both（都）Ann and Mary are suitable（适合）for the job.","eng":"Both Ann and Mary are suitable for the job.","zh-cn":"安妮和玛丽都适合干这项工作。"},{"eng-help":"Do you think you can do it by yourself（单独）?","eng":"Do you think you can do it by yourself?","zh-cn":"你认为你自己可以单独干完这件事吗?"},{"eng-help":"Have you seen Tom recently（最近）?","eng":"Have you seen Tom recently?","zh-cn":"最近你看见汤姆了吗？ "},{"eng-help":"He didn't need to attend（参加） the meeting.","eng":"He didn't need to attend the meeting.","zh-cn":"他没必要参加那个会议。"},{"eng-help":"He has a foreign friend（外国朋友） who lives in the United States（美国）.","eng":"He has a foreign friend who lives in the United States/ America.","zh-cn":"他有一个住在美国的外国朋友。"},{"eng-help":"He prefers coffee to tea.  （Prefer… to… 宁可要/更喜欢... 不要...)","eng":"He prefers coffee to tea.","zh-cn":"与茶相比，他更喜欢咖啡。"},{"eng-help":"He was very happy to hear from（收到...来信） his old friend.","eng":"He was very happy to hear from his old friend.","zh-cn":"他很高兴收到他的老朋友的信。"},{"eng-help":"How are you doing(你怎么样) these days?","eng":"How are you doing these days?","zh-cn":"这些日子你怎么样？"},{"eng-help":"I hope we can have some snow（下点雪） this winter.","eng":"I hope we can have some snow this winter.","zh-cn":"我希望今年冬天会下点雪。"},{"eng-help":"I hurried to（匆忙赶到） my office.","eng":"I hurried to my office.","zh-cn":"我匆忙赶到了办公室。"},{"eng-help":"I read the local newspapers（当地报纸） with great interest every evening.","eng":"I read the local newspapers with great interest every evening.","zh-cn":"每晚我怀着极大的兴趣读当地报纸。"},{"eng-help":"I slept soundly（睡得很熟） all night.","eng":"I slept soundly all night.","zh-cn":"我整夜睡得很熟。"},{"eng-help":"I've lost interest（失去兴趣） in my work.","eng":"I've lost interest in my work.","zh-cn":"我对这份工作已经失去了兴趣。"},{"eng-help":"She likes to help any one who（任何人） is in difficulty（有困难）.","eng":"She likes to help any one who is in difficulty.","zh-cn":"她乐意帮助任何一个有困难的人。"},{"eng-help":"The friend saw everything but did not say a single word（一言不发）.","eng":"The friend saw everything but did not say a single word.","zh-cn":"这位朋友看到了一切，却一言不发。"},{"eng-help":"This box can hold（容纳） more books than that one.","eng":"This box can hold more books than that one.","zh-cn":"这个箱子比那个箱子能装更多的书。"},{"eng-help":"Trees need water to grow（生长）.","eng":"Trees need water to grow.","zh-cn":"树木有水才能生长。"},{"eng-help":"We must take some measures（采取措施） to control（控制） the pollution（污染）.","eng":"We must take some measures to control the pollution.","zh-cn":"我们必须采取措施来控制污染。"},{"eng-help":"We should encourage（鼓励） him to have confidence（信心） in himself.","eng":"We should encourage him to have confidence in himself.","zh-cn":"我们应当鼓励他对自己要有信心。"},{"eng-help":"What kind of life（什么样的生活） do most people enjoy（喜欢）?","eng":"What kind of life do most people enjoy?","zh-cn":"多数人喜欢什么样的生活？"},{"eng-help":"What time do you go swimming every day?","eng":"What time do you go swimming every day?","zh-cn":"你每天何时去游泳？"},{"eng-help":"Who can help me clean the room（打扫房间）?","eng":"Who can help me clean the room?","zh-cn":"谁能帮我打扫房间？"},{"eng-help":"Would you mind closing the window for me?","eng":"Would you mind closing the window for me?","zh-cn":"能帮我关一下窗户吗？"},{"eng-help":"You and your team（团队） can discover（发现） the answers to problems（问题的答案） together.","eng":"You and your team can discover the answers to problems together.","zh-cn":"你和你的团队可一起找到问题的答案。"},{"eng-help":"You'd better（最好） do that again.","eng":"You'd better do that again.","zh-cn":"你最好再做一遍。"},{"eng-help":"A lot of natural resources in the mountain area are to be（有待于） exploited and used.","eng":"A lot of natural resources in the mountain area are to be exploited and used.","zh-cn":"那个山区有许多自然资源有待于开发利用。"},{"eng-help":"Life is meaningless（无意义） without a purpose（目标）.","eng":"Life is meaningless without a purpose.","zh-cn":" 没有目标的生活是毫无意义的。"},{"eng-help":"All that glitters（闪光） is not gold. (All… is not…,未必都是…)","eng":"All that glitters is not gold.","zh-cn":"闪光的未必都是金子。"},{"eng-help":"My classmate is much cleverer than（比…聪明的多） I.","eng":"My classmate is much cleverer than I.  ","zh-cn":"我的同学比我聪明多了。  "},{"eng-help":"As is known to all, China is the largest（最大的） developing country（发展中国家） in the world.","eng":"As is known to all, China is the largest developing country in the world.","zh-cn":"众所周知，中国是世界上最大的发展中国家。"},{"eng-help":"Apples here like water and sunshine.","eng":"Apples here like water and sunshine.","zh-cn":"这里的苹果喜欢水和阳光。"},{"eng-help":"Bill hit（撞） his car into a wall last night.","eng":"Bill hit his car into a wall last night.","zh-cn":"昨晚比尔开车时,车撞到了墙上。"},{"eng-help":"Could you tell me where the post office(邮局) is?","eng":"Could you tell me where the post office is?","zh-cn":"请问邮局在哪里？"},{"eng-help":"Each time history repeats（重复） itself, the price（代价） goes up（上升，增加）.","eng":"Each time history repeats itself, the price goes up.","zh-cn":"历史每重演一次，代价就增加（一分）。"},{"eng-help":"I was having a nap（睡觉） when suddenly the telephone rang（响铃）.","eng":"I was having a nap when suddenly the telephone rang.","zh-cn":"我在睡觉时，电话铃突然响了。"},{"eng-help":"If you decided to learn a new language, you would have to devote all your efforts to it（全力以赴）.","eng":"If you decided to learn a new language, you would have to devote all your efforts to it.","zh-cn":"如果你决定学一门新的语言，你必须全力以赴。"},{"eng-help":"I'm thinking about（考虑）a visit to Paris.","eng":"I'm thinking about a visit to Paris.","zh-cn":"我在考虑去巴黎旅游。"},{"eng-help":"In an age（时代） of plenty（充足）, we feel spiritually hungry（精神上的饥渴）.","eng":"In an age of plenty, we feel spiritually hungry.","zh-cn":"在这个物质财富充裕的时代，我们感到精神上的饥渴。"},{"eng-help":"In no other country（其它国家都没有） in the world can you find such plants as this one（像这样的植物）.","eng":"In no other country in the world can you find such plants as this one.","zh-cn":"像这种的植物在世界上的其它任何一个国家中都找不到。"},{"eng-help":"John and his brother differ（不同） in personality（个性） even if（尽管） their differences（差别） in age （年龄）are not significant（不大）.","eng":"John and his brother differ in personality even if their differences in age are not significant.","zh-cn":"尽管约翰和他哥哥在年纪上相差不大，但他们的个性却不相同。"},{"eng-help":"Our textbooks（教材） are very different from（与...很不一样） theirs.","eng":"Our textbooks are very different from theirs.","zh-cn":"我们的教材和他们的教材很不一样。"},{"eng-help":"Ted and William have lived under the same roof(同一屋檐下) for five years.","eng":"Ted and William have lived under the same roof for five years.","zh-cn":"泰德和威廉已经在同一个屋檐下生活了五年了。"},{"eng-help":"The doctor told me to have more water（多喝水）.","eng":"The doctor told me to have more water.","zh-cn":"医生让我多喝些水。"},{"eng-help":"The more passions（激情） we have, the more happiness we are likely（可能） to experience（体验）. (the more… the more 越多… 越多）","eng":"The more passions we have, the more happiness we are likely to experience.","zh-cn":"我们的激情越多，我们有可能体验的快乐就越多。"},{"eng-help":"The students are encouraged（被鼓励） to do more listening, reading and writing by their teacher.","eng":"The students are encouraged to do more listening, reading and writing by their teacher.","zh-cn":"老师鼓励学生多听、多读、多写。"},{"eng-help":"There is a large amount（大量） of energy（能量） wasted（浪费，损耗） due to（由于） friction(摩擦).","eng":"There is a large amount of energy wasted due to friction(摩擦).","zh-cn":"由于摩擦而损耗了大量的能量。"},{"eng-help":"They thought that there must be something wrong with（出毛病） their TV set.","eng":"They thought that there must be something wrong with their TV set.","zh-cn":"他们认为电视机一定出了毛病了。"},{"eng-help":"This place has plentiful（丰富的） material resources（物质资源）.","eng":"This place has plentiful material resources.","zh-cn":"这个地方的物质资源是丰富的。"},{"eng-help":"Transistors（晶体管） are small in size and light in weight.","eng":"Transistors are small in size and light in weight.","zh-cn":"晶体管的体积小，重量轻。"},{"eng-help":"Various substances（各种材料） differ widely in（有很大不同） their magnetic（磁性的） characteristics（特性）. ","eng":"Various substances differ widely in their magnetic（磁性的） characteristics. ","zh-cn":"各种材料的磁性有很大的不同。"},{"eng-help":"Would you please help me with（帮我...） this heavy(重的) box?","eng":"Would you please help me with this heavy box?","zh-cn":"你能帮我抬一下这个很沉的箱子吗？"},{"eng-help":"Fred was such a hardworking（努力学习的） student that he soon came out first（成为第一名）in the class. （such… that 如此…以至，表前后因果关系）","eng":"Fred was such a hardworking student that he soon came out first in the class.","zh-cn":"弗瑞德是一个学习十分用功的学生，所以不久他就成了班里学习最好的学生。"},{"eng-help":"You needn’t go there anymore. He already knows about it.","eng":"You needn't go there anymore. He already knows about it.","zh-cn":"你不必去了，他已经知道那件事了。"},{"eng-help":"He has taught English in this university ever since(自从) he moved to this city. ","eng":"He has taught English in this university ever since he moved to this city. ","zh-cn":"他自从移居到这座城市以来就一直在这所大学教英语。"},{"eng-help":"A lot of natural resources（自然资源） in the mountain area will be exploited（开发） and used.","eng":"A lot of natural resources in the mountain area will be exploited and used.","zh-cn":"那个山区有许多自然资源有待于开发利用。"},{"eng-help":"Air pollution（污染） is more serious（严重的） than water pollution.  ","eng":"Air pollution is more serious than water pollution.  ","zh-cn":"空气污染比水污染更严重。   "},{"eng-help":"I'm looking forward to（盼望着）your visit to China.  ","eng":"I'm looking forward to your visit to China.  ","zh-cn":"我在盼望你对中国的访问。   "},{"eng-help":"Wisdom(智慧) is much more important than knowledge（知识）.  ","eng":"Wisdom is much more important than knowledge.  ","zh-cn":"智慧比知识重要得多。"},{"eng-help":" The more you know, the further(更远的) you go. ","eng":" The more you know, the further you go. ","zh-cn":" 你懂得越多，就走得越远。 "},{"eng-help":" Tom is five years younger（较年轻的） than Peter. ","eng":" Tom is five years younger than Peter. ","zh-cn":" 汤姆比彼得小5岁。 "},{"eng-help":" Jim will go to his teacher whenever（无论何时） he has questions. ","eng":" Jim will go to his teacher whenever he has questions. ","zh-cn":" 吉姆一有问题就找他的老师。 "},{"eng-help":" Take an umbrella（雨伞） with you in case of（以防万一） rain. ","eng":" Take an umbrella with you in case of rain. ","zh-cn":" 带上雨伞以防下雨。 "},{"eng-help":" It doesn't matter（有关系） whether（是否） he comes or not. ","eng":" It doesn't matter whether he comes or not. ","zh-cn":" 他来或不来都无关紧要。 "},{"eng-help":" Success（成功） in business depends on（依靠） hard work. ","eng":" Success in business depends on hard work. ","zh-cn":" 生意上的成功取决于努力工作。 "},{"eng-help":" Wait here for further（进一步的） information. ","eng":" Wait here for further information. ","zh-cn":" 在此等待进一步消息。 "},{"eng-help":" Bob and Peter found out（发现） that they were twin（双胞胎） brothers. ","eng":" Bob and Peter found out that they were twin brothers. ","zh-cn":" 鲍勃和皮特发现他们是双胞胎兄弟。 "},{"eng-help":"You ought not to （不应该）smoke so much.","eng":"You ought not to smoke so much.","zh-cn":"你不应该抽这么多的烟。"},{"eng-help":"Of course they did not always（不总是） have money to buy the things they wanted.","eng":"Of course they did not always have money to buy the things they wanted.","zh-cn":"当然他们不总是有钱去买他们想要的东西。"},{"eng-help":"But I've got room（空间） for only two of you in the house.  ","eng":"But I've got room for only two of you in the house.  ","zh-cn":"但是我家里只能住下你们其中的两个人。   "},{"eng-help":"But Little Smart (小灵通) is not really that smart（聪明）.  ","eng":"But Little Smart (小灵通) is not really that smart.  ","zh-cn":"但小灵通并不是真的那么聪明。   "},{"eng-help":"By 1900, most states（州） had laws against（禁止） selling cigarettes（香烟） to young people.  ","eng":"By 1900, most states had laws against selling cigarettes to young people.  ","zh-cn":"到1900年时，大部分州都已经颁布了禁止向青少年出售香烟的法律。   "},{"eng-help":"Each year（每年） some of his money is given to the best scientists and writers of the world.  ","eng":"Each year some of his money is given to the best scientists and writers of the world.  ","zh-cn":"每年他的一部分钱都会奖/发给世界上最优秀的科学家和作家。   "},{"eng-help":"Fewer high school students are smoking now than a few years ago.  （比…少）","eng":"Fewer high school students are smoking now than a few years ago.  ","zh-cn":"现在中学生吸烟的人数比几年前要少了。   "},{"eng-help":"From the East Coast（东海岸） to the West Coast it is about 3,000 miles wide.  ","eng":"From the East Coast to the West Coast it is about 3,000 miles wide.  ","zh-cn":"美国领土的东西宽度约为3000英里。   "},{"eng-help":"He called the doctor and made sure（确定） they would meet（见面） at five.  ","eng":"He called the doctor and made sure they would meet at five.  ","zh-cn":"他给医生打了电话，定下来五点见面。   "},{"eng-help":"He often went from town to town（从一个城镇到另一个城镇） giving lectures（演讲）.  ","eng":"He often went from town to town giving lectures.  ","zh-cn":"他经常辗转在城镇间作演讲。   "},{"eng-help":"He was happy with（对...满意） his new car, and drove to work(开车上班) in it the next day.  ","eng":"He was happy with his new car, and drove to work in it the next day.  ","zh-cn":"他对自己的新车感到很满意，第二天就开着去上班了。   "},{"eng-help":"I am writing this letter to complain(投诉) about the service（服务） in your hotel.  ","eng":"I am writing this letter to complain about the service in your hotel.  ","zh-cn":"我写这封信的目的是要投诉你们旅馆的服务。   "},{"eng-help":"I saved（救了） the baby and became a hero（英雄）.  ","eng":"I saved the baby and became a hero.  ","zh-cn":"我因救了这个婴儿而成了英雄。   "},{"eng-help":"I used the bag to protect（保护）my face from（挡住） the smoke and heat.  ","eng":"I used the bag to protect my face from the smoke and heat.  ","zh-cn":"我用包捂在脸上挡住烟和热。   "},{"eng-help":"If you travel by ship across the Pacific（穿越太平洋）, you cross the international date line(国际日期变更线).  ","eng":"If you travel by ship across the Pacific, you cross the international date line(国际日期变更线).  ","zh-cn":"如果你乘船航行穿越太平洋，你会穿过国际日期变更线。   "},{"eng-help":"In most countries, animals are in a worse position（较差的处境）than human beings.","eng":"In most countries, animals are in a worse position than human beings.","zh-cn":"在大多数国家，动物的境况比人类要差。   "},{"eng-help":"It is no good（没有意义） hoping to read all these books.","eng":"It is no good hoping to read all these books.","zh-cn":"希望能够读完所有这些书是毫无意义的。   "},{"eng-help":"Jim was clever, but he does not work hard（工作不努力）.  ","eng":"Jim was clever, but he does not work hard.  ","zh-cn":"吉姆很聪明，但他讨厌吃苦。   "},{"eng-help":"Many schools will open for lessons（开学） at the beginning of September（九月）.  ","eng":"Many schools will open for lessons at the beginning of September.  ","zh-cn":"很多学校九月初开学。   "},{"eng-help":"On their way（在路上） they came to a bread shop.  ","eng":"On their way they came to a bread shop.  ","zh-cn":"在途中，他们来到一家面包店。   "},{"eng-help":"Only on weekends, cars can not go into the Central Park（中央公园）.  ","eng":"Only on weekends, cars can not go into the Central Park.  ","zh-cn":"只有在周末，中央公园不许汽车入内。   "},{"eng-help":"Playing the sport you like（喜欢的运动） is an important part（重要部分） of a person's life.  ","eng":"Playing the sport you like is an important part of a person's life.  ","zh-cn":"进行一项自己喜欢的运动是一个人生活中重要的一部分。   "},{"eng-help":"She could not understand（理解）me when I spoke to her.  ","eng":"She could not understand me when I spoke to her.  ","zh-cn":"当我跟她讲话的时候，她根本听不懂我的话。   "},{"eng-help":"Since（因为） the earth looks like a ball, the sun can shine（照耀）on only half（一半） of it at a time（一次）.  ","eng":"Since the earth looks like a ball, the sun can shine on only half of it at a time.  ","zh-cn":"由于地球看上去像一个球，太阳一次只能照到它的一半。   "},{"eng-help":"Some of her pictures（图画）were shown in an art exhibition (展览会) in Shanghai when she was 4 years old.  ","eng":"Some of her pictures were shown in an art exhibition (展览会) in Shanghai when she was 4 years old.  ","zh-cn":"四岁的时候她的一些画就在上海的一个艺术展览会上展出。   "},{"eng-help":"Some parents（父母） even（甚至） do not let their children meet their good friends.  ","eng":"Some parents even do not let their children meet their good friends.  ","zh-cn":"有些父母甚至不让自己的孩子去见他们的好朋友。   "},{"eng-help":"The school plans games and matches for its students.  ","eng":"The school plans games and matches for its students.  ","zh-cn":"学校为学生们安排游戏和比赛。   "},{"eng-help":"Their parents don't know（了解） them as well as（像…一样） their friends do.  ","eng":"Their parents don't know them as well as their friends do.  ","zh-cn":"他们的父母不像朋友那样了解他们。   "},{"eng-help":"They questioned（审问） both men but neither（两者都不） of them could speak English.  ","eng":"They questioned both men but neither of them could speak English.  ","zh-cn":"他们审问这两名男子，但这两个人都不会说英语。   "},{"eng-help":"We cannot tell（判断，说出） when an earthquake（地震）is coming.  ","eng":"We cannot tell when an earthquake（地震）is coming.  ","zh-cn":"我们无法判断地震什么时候发生。   "},{"eng-help":"\"Why does the parrot (鹦鹉) cost so much（这么贵）?\" the man asked.  ","eng":"\"Why does the parrot (鹦鹉) cost so much?\" the man asked.  ","zh-cn":"这个人问道：“这只鹦鹉为什么这么贵？”。   "},{"eng-help":"Who would say like this（像这样）?","eng":"Who would say like this?","zh-cn":"谁会这样说呢？ "},{"eng-help":"When she left（离开） school, she went first（首先） to Britain（英国）.","eng":"When she left school, she went first to Britain.","zh-cn":"在她离开学校后，她一开始去的是英国。"},{"eng-help":"What did they say they had done（他们干了什么）?","eng":"What did they say they had done?","zh-cn":"他们说他们干了什么？"},{"eng-help":"The couple（夫妇） enjoyed their holiday（假期过得很愉快） very much.","eng":"The couple enjoyed their holiday very much.","zh-cn":"这对夫妇假期过得很快活。"},{"eng-help":"He didn't understand what the teacher said（老师说的话）.","eng":"He didn't understand what the teacher said.","zh-cn":"他不懂老师所说的话。"},{"eng-help":"If you have time, you can come to my office.","eng":"If you have time, you can come to my office.","zh-cn":"如果有时间，你可以来我的办公室。"},{"eng-help":"I think the picture shows us how fruit（水果） is necessary（必须的） to life.","eng":"I think the picture shows us how fruit is necessary to life.","zh-cn":"我想这幅图是告诉我们水果对生活是不可缺的。"},{"eng-help":"China will build up a space station（太空站） in ten years.","eng":"China will build up a space station in ten years.","zh-cn":"在十年内，中国将建一个太空站。"},{"eng-help":"Do you often do your washing（洗衣服） in the evening?","eng":"Do you often do your washing in the evening?","zh-cn":"你经常晚上洗衣服吗？"},{"eng-help":"Don't you mind（介意） my opening the window（打开窗户）?","eng":"Don't you mind my opening the window?","zh-cn":"你不介意我打开窗户吧？"},{"eng-help":"Each student needs to learn something different（不同的东西）.","eng":"Each student needs to learn something different.","zh-cn":"每个学生需要学一些不同的东西。"},{"eng-help":"Excuse me. Where's the nearest （最近的） police station（派出所）?","eng":"Excuse me. Where's the nearest police station?","zh-cn":"劳驾，离这儿最近的派出所在哪里？"},{"eng-help":"He lives in a small room with only（只有）one small window.","eng":"He lives in a small room with only one small window.","zh-cn":"他住在一间只有一个小窗户的小房间里。"},{"eng-help":"How long have you collected your stamps（集邮）?","eng":"How long have you collected your stamps?","zh-cn":"你集邮有多久了？"},{"eng-help":"I can't go with you today because I'm too busy. ","eng":"I can't go with you today because I'm too busy. ","zh-cn":"我今天不能和你一块儿去，因为我太忙了。"},{"eng-help":"I prefer tea（茶） to coffee（咖啡）.          （Prefer… to… 宁可要/更喜欢... 不要...)","eng":"I prefer tea to coffee.","zh-cn":"我宁可要茶，不要咖啡。"},{"eng-help":"I think our dream(梦想) will become true（实现）.","eng":"I think our dream will become true.","zh-cn":"我认为我们的梦想将成为现实。"},{"eng-help":"Jack works much harder than（比...努力多了）before.","eng":"Jack works much harder than before.","zh-cn":"杰克学习比以前努力多了。"},{"eng-help":"My grandpa is used to(习惯) getting up early.","eng":"My grandpa is used to getting up early.","zh-cn":"我爷爷习惯早起。"},{"eng-help":"Please move your chair to （移到）the window.","eng":"Please move your chair to the window.","zh-cn":"请把你的椅子移到窗口。"},{"eng-help":"Please ring me up（给我打电话） tomorrow morning.","eng":"Please ring me up tomorrow morning.","zh-cn":"请明天上午给我打电话。"},{"eng-help":"Take this empty（空的） box away and bring（拿来） me a full（满的） one.  (take...away 拿走)","eng":"Take this empty box away and bring me a full one.","zh-cn":"把这只空盒子拿走，给我拿一个满的来。"},{"eng-help":"Telling the way（指路） to a place for free(免费).","eng":"Telling the way to a place for free.","zh-cn":"免费指路。"},{"eng-help":"The doctor said there was nothing wrong with（没毛病） his eyes.","eng":"The doctor said there was nothing wrong with his eyes.","zh-cn":"医生说他的眼睛没有毛病。"},{"eng-help":"The plane took off（起飞） very smoothly（平稳地）.","eng":"The plane took off very smoothly.","zh-cn":"飞机很平稳地起飞了。"},{"eng-help":"They sold（卖了） the old house（老房子） yesterday.","eng":"They sold the old house yesterday.","zh-cn":"昨天他们把那旧房子卖了。"},{"eng-help":"We all like（喜欢） her because she's very helpful（乐于助人）.","eng":"We all like her because she's very helpful.","zh-cn":"我们都喜欢她， 因为她乐于助人。"},{"eng-help":"Without water（没有水）, there is no life（没有生命）on the earth（地球）.","eng":"Without water, there is no life on the earth.","zh-cn":"没有水，地球上就没有生命。"},{"eng-help":"A dog is always well-known （出名的）as a clever and friendly animal.","eng":"A dog is always well-known as a clever and friendly animal.","zh-cn":"在动物中，狗素以其聪明与和善而著称。"},{"eng-help":"A good memory（记性好） is a great help in learning a language（学习语言）. ","eng":"A good memory is a great help in learning a language. ","zh-cn":"好的记忆有助于语言的学习。"},{"eng-help":"A sign（站牌） at the bus stop(公车站) helps you to know which bus to take（乘坐）.","eng":"A sign at the bus stop helps you to know which bus to take.","zh-cn":"公交车站的站牌告诉你要乘哪趟车。"},{"eng-help":"But competitive swimming（游泳比赛） is just（只有） over one hundred years old.","eng":"But competitive swimming is just over one hundred years old.","zh-cn":"但游泳比赛不过只有一百余年的历史。"},{"eng-help":"Everyone was in bed（上床睡觉） except me（除我之外）.","eng":"Everyone was in bed except me.","zh-cn":"除了我，所有人都上床睡觉了。"},{"eng-help":"Excuse me. Could you tell me how to（如何） get to the cinema（到电影院）?","eng":"Excuse me. Could you tell me how to get to the cinema?","zh-cn":"请问去电影院怎么走？"},{"eng-help":"Go down（顺着走） this street until（直到） you reach the second traffic lights（第二个交通订）.","eng":"Go down this street until you reach the second traffic lights.","zh-cn":"顺着这条街走，一直走到第二个交通灯处。"},{"eng-help":"He said that he had left（忘下） his umbrella（雨伞） in the library（图书馆）.","eng":"He said that he had left his umbrella in the library.","zh-cn":"他说他把伞忘在图书馆了。"},{"eng-help":"He was pleased with（因...高兴） the fine weather(好天气).","eng":"He was pleased with the fine weather.","zh-cn":"他因天气好而感到快乐。"},{"eng-help":"I think she will change her mind（改主意） tomorrow.","eng":"I think she will change her mind tomorrow.","zh-cn":"我想明天她就会改变主意。"},{"eng-help":"I was too excited to say （太兴奋而说不出） a word in front of him（在他面前）.","eng":"I was too excited to say a word in front of him.","zh-cn":"我当时太兴奋了，以至于在他面前什么也说不出来了。"},{"eng-help":"I'll write to you（写信给你） as soon as I get there.","eng":"I'll write to you as soon as I get there.","zh-cn":"我一到那儿就给你写信。"},{"eng-help":"I'm going to visit a friend of mine（我的一位朋友） this Sunday（本周日）.","eng":"I'm going to visit a friend of mine this Sunday.","zh-cn":"本星期天，我要去拜访一位朋友。"},{"eng-help":"It is certain（肯定的） that we shall produce this kind of engine（生产这种发动机）.","eng":"It is certain that we shall produce this kind of engine.","zh-cn":"我们将生产这种发动机，这是肯定无疑的。"},{"eng-help":"It is necessary to take medicine（吃药） on time（按时）.","eng":"It is necessary to take medicine on time.","zh-cn":"按时吃药是必要的。"},{"eng-help":"My problem（问题） is that I don't have much time（没有多少时间） to do the work.","eng":"My problem is that I don't have much time to do the work.","zh-cn":"我的问题在于我没有多少时间干这工作。"},{"eng-help":"Not all（不全是） the machines here are produced by our factory. ","eng":"Not all the machines here are produced by our factory. ","zh-cn":"这里的机器并不全是我厂生产的。"},{"eng-help":"Nothing in the world moves faster than（比…更快） light（光）. ","eng":"Nothing in the world moves faster than light. ","zh-cn":"世界上没有什么东西比光走得更快。"},{"eng-help":"Our library is as big as（像…一样大） theirs.","eng":"Our library is as big as theirs.","zh-cn":"我们的图书馆和他们的一样大。"},{"eng-help":"She always laughs at（因…而笑） a good joke(好笑话).","eng":"She always laughs at a good joke.","zh-cn":"她听到了好的笑话，总是要笑的。"},{"eng-help":"Stay here（留在这里） before I get back（回来）.","eng":"Stay here before I get back.","zh-cn":"在我回来之前别离开。"},{"eng-help":"The polluted（污染的） air becomes poisonous（有毒） and dangerous to health（有害健康）.","eng":"The polluted air becomes poisonous and dangerous to health.","zh-cn":"污染的空气变得有毒，对人的健康有害。"},{"eng-help":"The shop is open till（直到） ten at night every day.","eng":"The shop is open till ten at night every day.","zh-cn":"商店每天营业到晚上十点。"},{"eng-help":"Think it over（仔细考虑） before you speak.","eng":"Think it over before you speak.","zh-cn":"先考虑一下再说。"},{"eng-help":"We are getting ready（做准备） for our English examination（考试）.","eng":"We are getting ready for our English examination.","zh-cn":"我们正在为英语考试做准备。"},{"eng-help":"A teacher should have patience（有耐心） in his work. ","eng":"A teacher should have patience in his work. ","zh-cn":"当老师应当有耐心。"},{"eng-help":"Can you express（表达） yourself clearly（清楚地） in English?","eng":"Can you express yourself clearly in English?","zh-cn":"你能用英语清楚地表达自己的意思吗？"},{"eng-help":"He had to leave（离开） early yesterday.","eng":"He had to leave early yesterday.","zh-cn":"昨天他不得不早些离开。"},{"eng-help":"When the meeting will be held（何时举行） has not been decided（决定） yet.","eng":"When the meeting will be held has not been decided yet.","zh-cn":"会议何时举行尚未决定。"},{"eng-help":"Would you mind（介意） turning down（调小音量） the radio?","eng":"Would you mind turning down the radio?","zh-cn":"你不介意把广播声调小一点吧？"},{"eng-help":"You can practise（练习） with your classmates outside class（课外）.","eng":"You can practise with your classmates outside class.","zh-cn":"你可以在课外和同学一起练习。"},{"eng-help":"He has been learning（一直在学） English for years.","eng":"He has been learning English for years.","zh-cn":"多年来，他一直在学英语。"},{"eng-help":"He has the habit（习惯） of making notes（做笔记） while reading（在阅读时）.","eng":"He has the habit of making notes while reading.","zh-cn":"他有阅读时记笔记的习惯"},{"eng-help":"He is always making excuses（找借口） for being late.","eng":"He is always making excuses for being late.","zh-cn":"他总是为他的迟到找借口。"},{"eng-help":"He is thinking about（正在考虑） moving to a new place.","eng":"He is thinking about moving to a new place.","zh-cn":"他正在考虑搬到一个新的的地方"},{"eng-help":"He keeps looking at（总是在看） himself in the mirror（镜子）.","eng":"He keeps looking at himself in the mirror.","zh-cn":"他老是照镜子。"},{"eng-help":"I look forward to（期待，盼望） hearing from you.","eng":"I look forward to hearing from you.","zh-cn":"我期待着收到您的回信。"},{"eng-help":"I'm certain he'll go to see the film, because he's bought a ticket（买好了票）.","eng":"I'm certain he'll go to see the film, because he's bought a ticket.","zh-cn":"我肯定他会去看电影的，因为他把票都买好了。"},{"eng-help":"I'm finishing（即将完成） my second（第二） year of studies.","eng":"I'm finishing my second year of studies.","zh-cn":"我即将完成第二年的学习。"},{"eng-help":"Open the window and let the fresh air in.","eng":"Open the window and let the fresh air in.","zh-cn":"打开窗户让新鲜空气进来。"},{"eng-help":"Promise（答应） me never（决不） to be late（迟到） again.","eng":"Promise me never to be late again.","zh-cn":"答应我以后再也不要迟到了。"},{"eng-help":"Riding bicycles（骑自行车） can save energy（节约能源）.","eng":"Riding bicycles can save energy.","zh-cn":"骑自行车能够节约能源。"},{"eng-help":"She is used to（习惯） living in the countryside（乡下）.","eng":"She is used to living in the countryside.","zh-cn":"她习惯住在乡下。"},{"eng-help":"She likes Mike a lot, but she doesn't want to get married（结婚） so early.","eng":"She likes Mike a lot, but she doesn't want to get married so early.","zh-cn":"她喜欢迈克，但她不想那么早就结婚。"},{"eng-help":"He stopped smoking. （stop doing sth. 停止做某事）","eng":"He stopped smoking.","zh-cn":"他已经戒烟了。"},{"eng-help":"He stopped to smoke. （stop to do sth. 停下来，去做某事）","eng":"He stopped to smoke.","zh-cn":"他停下来，开始吸烟。"},{"eng-help":"The cause（原因） of the fire was carelessness（粗心大意）.","eng":"The cause of the fire was carelessness.","zh-cn":"起火的原因是不谨慎。"},{"eng-help":"The doctor is very patient with（耐心对待...） his patients（病人）.","eng":"The doctor is very patient with his patients.","zh-cn":"医生对他的病人很耐心。"},{"eng-help":"The earth（地球） on which we live is shaped(形状) like a ball.","eng":"The earth on which we live is shaped like a ball.","zh-cn":"我们居住的地球，形状像一个大球。"},{"eng-help":"The moon looks much bigger than any other stars（任何其它星星） in the sky.","eng":"The moon looks much bigger than any other stars in the sky.","zh-cn":"月亮看起来比天空中任何其他星星都要大的多。"},{"eng-help":"This is the most wonderful（美好的） day of my life, because I'm here with you（与你在一起） now.","eng":"This is the most wonderful day of my life, because I'm here with you now.","zh-cn":"今天是我一生中最美好的一天，因为我现在和你在一起。"},{"eng-help":"Travel broadens（开阔） the mind.","eng":"Travel broadens the mind.","zh-cn":"旅行可以开阔眼界。"},{"eng-help":"We should make best use（充分利用） of time.","eng":"We should make best use of time.","zh-cn":"我们要充分利用时间。"},{"eng-help":"We used to live（曾经住在） near a big park.","eng":"We used to live near a big park.","zh-cn":"我们曾经住在一个大公园附近。"},{"eng-help":"When are you going to get your hair cut（去理发）?","eng":"When are you going to get your hair cut?","zh-cn":"你打算什么时候去理发？"},{"eng-help":"When I was young, I'd listen to the radio, waiting for（等待） my favorite（最喜爱的）songs.","eng":"When I was young, I'd listen to the radio, waiting for my favorite songs.","zh-cn":"当我还是个小孩的时候，我常听收音机，等待我最喜欢的歌。"},{"eng-help":"When were the Olympic Games（奥运会） founded（创办）?","eng":"When were the Olympic Games founded?","zh-cn":"奥运会是何时创办的？"},{"eng-help":"You'll find in English some words can be remembered（记住） more easily than（比…更容易） the others（别的单词）.","eng":"You'll find in English some words can be remembered more easily than the others.","zh-cn":"你会发现英语里有些词比别的词容易记忆。"},{"eng-help":"I am very familiar（熟悉） with his name.","eng":"I am very familiar with his name.","zh-cn":"我很熟悉他的名字。"},{"eng-help":"I came back because of（由于） the rain.","eng":"I came back because of the rain.","zh-cn":"因为下雨我就回来了。"},{"eng-help":"He sits there listening to music.","eng":"He sits there listening to music.","zh-cn":"他坐在那儿听音乐。"},{"eng-help":"He had a traffic accident（交通事故） last week.","eng":"He had a traffic accident last week.","zh-cn":"他上星期出了一起交通事故。"},{"eng-help":"He is always very active（积极） in student activities（活动）.","eng":"He is always very active in student activities.","zh-cn":"他在学生活动中一直非常积极。"},{"eng-help":"He offered to help（主动帮助） us with our work.","eng":"He offered to help us with our work.","zh-cn":"他主动帮助我们工作。"},{"eng-help":"I am glad（高兴） to hear the good news.","eng":"I am glad to hear the good news.","zh-cn":"听到这个好消息我很高兴。"},{"eng-help":"Beijing Olympic Games can make Chinese people work harder（使…更努力工作）.","eng":"Beijing Olympic Games can make Chinese people work harder.","zh-cn":"北京奥林匹克运动会使得中国人民更加努力工作。"},{"eng-help":"The store（商店） opens from 7 am. to 8 pm.","eng":"The store opens from 7 am. to 8 pm.","zh-cn":"这个商店从上午7点到下午8点开门营业。"},{"eng-help":"The TV set（电视机） cost me $200.","eng":"The TV set cost me $200.","zh-cn":"这台电视机花了我200美元。"},{"eng-help":"We are going to play golf（打高尔夫） this Sunday.","eng":"We are going to play golf this Sunday.","zh-cn":"我们这个星期天要去打高尔夫球。"},{"eng-help":"I feel lonely（感到孤独） without you.","eng":"I feel lonely without you.","zh-cn":"没有你我感到孤独。"},{"eng-help":"I feel satisfied with my life.","eng":"I feel satisfied with my life.","zh-cn":"我对生活感到满意。"},{"eng-help":"I got married ten years ago（以前）.","eng":"I got married ten years ago.","zh-cn":"十年前，我结了婚。"},{"eng-help":"I had no choice（选择）.","eng":"I had no choice.","zh-cn":"我别无选择。"},{"eng-help":"I have a pain（疼痛） in my back（背部）.","eng":"I have a pain in my back.","zh-cn":"我的背疼。"},{"eng-help":"I'm going to buy a monthly ticket（月票） tomorrow.","eng":"I'm going to buy a monthly ticket tomorrow.","zh-cn":"我想明天买张月票。"},{"eng-help":"I'm having a headache（头疼） now.","eng":"I'm having a headache now.","zh-cn":"我现在有点头疼。"},{"eng-help":"I'm not an English major（英语专业） student.","eng":"I'm not an English major student.","zh-cn":"我不是英语专业的学生。"},{"eng-help":"It is not necessary（没有必要） to do this work.","eng":"It is not necessary to do this work.","zh-cn":"没有必要做这项工作。"},{"eng-help":"It's still（仍然，还） raining today!","eng":"It's still raining today!","zh-cn":"今天还在下雨。"},{"eng-help":"My classmate is more clever than I.","eng":"My classmate is more clever than I.","zh-cn":"我的同学比我聪明。"},{"eng-help":"Students can study by themselves（自学） through（通过） school network（网络）.","eng":"Students can study by themselves through school network.","zh-cn":"学生可以自己通过校园网络学习。"},{"eng-help":"The school management（管理） must be improved（改进）.","eng":"The school management must be improved.","zh-cn":"必须改进学校管理。"},{"eng-help":"We are from（来自） mainland  China（中国大陆）.","eng":"We are from mainland China.","zh-cn":"我们来自中国大陆。"},{"eng-help":"What a beautiful T-shirt（T恤衫） you have!","eng":"What a beautiful T-shirt you have!","zh-cn":"你的T恤衫真漂亮！"},{"eng-help":"What did you do last week?","eng":"What did you do last week?","zh-cn":"上星期你干什么了？"},{"eng-help":"What's your nationality（国籍）, sir?","eng":"What's your nationality, sir?","zh-cn":"先生，您是哪国人？"},{"eng-help":"When do you think John will arrive(到达)?","eng":"When do you think John will arrive?","zh-cn":"你以为约翰将于何时到达？"},{"eng-help":"Who's going to answer the door（开门）?","eng":"Who's going to answer the door?","zh-cn":"谁去开门？"},{"eng-help":"Who's going to answer the telephone（接电话）?","eng":"Who's going to answer the telephone?","zh-cn":"谁去接电话？"},{"eng-help":"Would you like（想要） some mineral water（矿泉水）?","eng":"Would you like some mineral water?","zh-cn":"你想要矿泉水吗？"},{"eng-help":"As is known to all, China is a developing country.","eng":"As is known to all, China is a developing country.","zh-cn":"众所周知，中国是一个发展中国家。"},{"eng-help":"Fires may do more damage than（比...造成更大损失） the earthquakes(地震).","eng":"Fires may do more damage than the earthquakes.","zh-cn":"火所造成的损失可能比地震还严重。"},{"eng-help":"He was satisfied with his new car, and drove to work in it the very next day.","eng":"He was satisfied with his new car, and drove to work in it the very next day.","zh-cn":"他对自己的新车感到很满意，第二天就开着去上班了。"},{"eng-help":"In Foreign Languages Department（外语系），a checking machine（阅卷机） is used to correct（批改） the students' test papers（试卷）.","eng":"In Foreign Languages Department，a checking machine is used to correct the students' test papers.","zh-cn":"外语教研部用阅卷机给学生批卷。"},{"eng-help":"In the world, soccer or football（足球） is the most popular（受欢迎） sport.","eng":"In the world, soccer or football is the most popular sport.","zh-cn":"足球是一项风靡全世界的体育运动。"},{"eng-help":"Though（尽管） it was late, they kept on（继续） working.","eng":"Though it was late, they kept on working.","zh-cn":"尽管已经很晚了，他们还在继续工作。"},{"eng-help":"We enjoyed ourselves（玩得很愉快） very much at the party.","eng":"We enjoyed ourselves very much at the party.","zh-cn":"在那次晚会上我们玩得很愉快。"},{"eng-help":"Please give this book to whoever comes first（最先来的人）.","eng":"Please give this book to whoever comes first.","zh-cn":"请把这本书给最先来的人。"},{"eng-help":"She was the only daughter（唯一的女儿）and the youngest child of five.","eng":"She was the only daughter and the youngest child of five.","zh-cn":"她是家里唯一的女儿，也是五个孩子中年龄最小的。"},{"eng-help":"The most common samples（普通样本） of inorganic materials（无机物） are the gases（气体） of the atmosphere（大气）, water, rocks（岩石）, etc. ","eng":"The most common samples of inorganic materials are the gases of the atmosphere, water, rocks, etc. ","zh-cn":"大气中的气体、水、岩石都是无机物中最普通的实例。"},{"eng-help":"Who do you believe（相信） will be given the prize（获奖）?","eng":"Who do you believe will be given the prize?","zh-cn":"你相信谁会得到这个奖？"},{"eng-help":"Whom did they expect（希望） the teacher would criticize（批评）?","eng":"Whom did they expect the teacher would criticize?","zh-cn":"他们希望老师批评谁？"},{"eng-help":"You do not have to（不必） understand（明白） everything all at once（一下子）.","eng":"You do not have to understand everything all at once.","zh-cn":"你不必一下子就明白所有的事。"},{"eng-help":"Christmas（圣诞节） is the most important festival（节日） in the west（西方）.","eng":"Christmas is the most important festival in the west.","zh-cn":"圣诞节是西方最重要的节日。"},{"eng-help":"How do you spell（拼写） your last name（姓）?","eng":"How do you spell your last name?","zh-cn":"你的姓是如何拼写的？"},{"eng-help":"I'll try not to take up（占用） too much of your time.","eng":"I'll try not to take up too much of your time.","zh-cn":"我尽量不占用你太多的时间。"},{"eng-help":"I'm wondering（想知道） who broke（打破） the window.","eng":"I'm wondering who broke the window.","zh-cn":"我纳闷谁打破了窗户。"},{"eng-help":"In the United States a lot of college students do not live at home（在家住）. ","eng":"In the United States a lot of college students do not live at home. ","zh-cn":"在美国，许多大学生不在家住。"},{"eng-help":"It's always a pleasure（高兴） to see many friends here in London（伦敦）.","eng":"It's always a pleasure to see many friends here in London.","zh-cn":"很高兴在伦敦见到许多朋友。"},{"eng-help":"It's never（决不会） too late to learn（太晚了而不能学）.","eng":"It's never too late to learn.","zh-cn":"活到老，学到老。"},{"eng-help":"Jane has told me a lot about her trip（旅行） in South Africa（南非）.","eng":"Jane has told me a lot about her trip in South Africa.","zh-cn":"简告诉我许多有关她到南非旅游的事。"},{"eng-help":"Many students who have problems won't go for advice（寻求建议） or help.","eng":"Many students who have problems won't go for advice or help.","zh-cn":"许多有问题的学生不会去寻找建议或帮助。"},{"eng-help":"No matter what happens（无论发生什么）, I'll be your best friend. ","eng":"No matter what happens, I'll be your best friend. ","zh-cn":"无论发生什么，我都是你最好的朋友。"},{"eng-help":"Please call me back(回我电话) when you are free（有空的）. ","eng":"Please call me back when you are free. ","zh-cn":"你有时间给我回个电话。"},{"eng-help":"Tom is not good at（学得不好） his subjects except math（除数学之外）.","eng":"Tom is not good at his subjects except math.","zh-cn":"除了数学以外，汤姆其他功课都不太好。"},{"eng-help":"Tom likes playing basket-ball but he can't play well.","eng":"Tom likes playing basket-ball but he can't play well.","zh-cn":"汤姆喜欢打篮球，但他打得不好。"},{"eng-help":"The old man is in his late sixties（接近七十岁）. ","eng":"The old man is in his late sixties. ","zh-cn":"老人近七十岁。"},{"eng-help":"The teacher assigned（布置作业） them to write a composition（作文）.","eng":"The teacher assigned them to write a composition.","zh-cn":"老师给他们布置的作业是写篇作文。"},{"eng-help":"Try hard or（否则） you'll fail（不及格） the test. ","eng":"Try hard or you'll fail the test. ","zh-cn":"努力地，否则你考试及不了格。"},{"eng-help":"We are short of（缺乏） time and money.","eng":"We are short of time and money.","zh-cn":"我资金和时间都缺。"},{"eng-help":"Before he turned to Internet for help（求助于）, Jack had spent（花费）one year looking for（寻找） his parents（父母）.","eng":"Before he turned to Internet for help, Jack had spent one year looking for his parents.","zh-cn":"在杰克求助于网络之前，他已经花了一年的时间寻找父母。"},{"eng-help":"Children who often exercise are cleverer（更聪明）.","eng":"Children who often exercise are cleverer.","zh-cn":"经常锻炼的孩子更聪明。"},{"eng-help":"He doesn't know what life means（人生的意义）to him.","eng":"He doesn't know what life means to him.","zh-cn":"他不知道人生的意义。"},{"eng-help":"He was too tired to（太累了而不能...） go on reading.","eng":"He was too tired to go on reading.","zh-cn":"他太困了，无法再阅读了。"},{"eng-help":"I do（确实） want to know what really happened. （do, 助动词，用来强调谓语动词“want”，翻译为\"确实，的确\"）","eng":"I do want to know what really happened.","zh-cn":"我确实想知道到底发生了什么事。"},{"eng-help":"I don't think he can（认为他不会） operate（操作） the new type（新型） of computer.  （don’t think,把否定移到后面动词上理解）","eng":"I don't think he can operate the new type of computer. ","zh-cn":"我认为他不会操作这种计算机。"},{"eng-help":"I expect（预料） she will have changed her mind（改主意） by tomorrow.","eng":"I expect she will have changed her mind by tomorrow.","zh-cn":"我预料到明天为止她会改变观点。"},{"eng-help":"People use science knowledge（科学知识） to understand and change nature. ","eng":"People use science knowledge to understand and change nature. ","zh-cn":"人们利用科学知识去了解自然，改造自然。"},{"eng-help":"The meal（这顿饭）was very good except that（除…之外） the fish was a bit too salty（太咸）.","eng":"The meal was very good except that the fish was a bit too salty.","zh-cn":"除了鱼有点太咸，这顿饭还是很好的。"},{"eng-help":"The more（越多） money I make（我赚钱）, the happier（越快乐） I will be.","eng":"The more money I make, the happier I will be.","zh-cn":"赚的钱越多，我就会越快乐。"},{"eng-help":"The whole process（过程） required（要求） a great deal（大量） of effort（努力）, skill（技巧） and a luck（运气）. ","eng":"The whole process required a great deal of effort, skill and a luck. ","zh-cn":"整个过程需要很大的努力，需要高度的技巧，还需要运气。"},{"eng-help":"We can learn from our mistakes（错误）.","eng":"We can learn from our mistakes.","zh-cn":"我们能从错误中学习。"},{"eng-help":"You ought not to（不应该） smoke so much.","eng":"You ought not to smoke so much.","zh-cn":"你不应该抽这么多的烟。"},{"eng-help":"Do you think we will have good weather?","eng":"Do you think we will have good weather?","zh-cn":"你认为我们会有好天气吗？"},{"eng-help":"He has sympathy（同情心） for all poor（穷苦的） people.","eng":"He has sympathy for all poor people.","zh-cn":"他对所有穷苦人都富有同情心。"},{"eng-help":"He is a worldwide famous（世界著名的） scientist.","eng":"He is a worldwide famous scientist.","zh-cn":"他是一位世界著名的科学家。"},{"eng-help":"He is sweating（出汗） all over（全身）.","eng":"He is sweating all over.","zh-cn":"他出了一身汗。"},{"eng-help":"Do you think students should do a part-time job（打工，兼职）?","eng":"Do you think students should do a part-time job?","zh-cn":"你认为学生应该业余打工吗？"},{"eng-help":"Do you want to go out or（或者） stay at home?","eng":"Do you want to go out or stay at home?","zh-cn":"你想出去还是留在家里？"},{"eng-help":"My watch（手表） is five minutes fast.","eng":"My watch is five minutes fast.","zh-cn":"我的表快了五分钟。"},{"eng-help":"I'm sorry to hear（听到…很遗憾） that.","eng":"I'm sorry to hear that.","zh-cn":"听到这消息我很遗憾。 "},{"eng-help":"I'm very much eager（渴望） to improve（提高） my oral English（英语口语）.","eng":"I'm very much eager to improve my oral English.","zh-cn":"我非常渴望改进我的英语口语。"},{"eng-help":"I haven't enough money for（钱够买） the coat.","eng":"I haven't enough money for the coat.","zh-cn":"我的钱不够买这件上衣。"},{"eng-help":"Please give me your homework before leaving（离开前）.","eng":"Please give me your homework before leaving.","zh-cn":"请在离开前将你的作业交给我。"},{"eng-help":"There is something urgent（急事） for you to do right now（立刻）.","eng":"There is something urgent for you to do right now.","zh-cn":"有件急事要你立即去做。"},{"eng-help":"They are ordinary（普通的） people, but did a great（伟大的） job.","eng":"They are ordinary people, but did a great job.","zh-cn":"他们是普通人，却干了一件大事。"},{"eng-help":"We must arrive there on time（按时，准时）.","eng":"We must arrive there on time.","zh-cn":"我们必须准时到那儿。"},{"eng-help":"This custom（风俗，习俗） has a long tradition（传统）.","eng":"This custom has a long tradition.","zh-cn":"这一风俗历史悠久。"},{"eng-help":"When I was young, I liked singing and dancing.","eng":"When I was young, I liked singing and dancing.","zh-cn":"年轻时，我喜欢唱歌跳舞。"},{"eng-help":"As long as（只要）there is water, plants(植物) won't die（死亡） quickly.","eng":"As long as there is water, plants won't die quickly.","zh-cn":"只要有水，植物就不会很快死亡。"},{"eng-help":"I guess（猜） he is ill.","eng":"I guess he is ill.","zh-cn":"我猜他大概是病了。"},{"eng-help":"What we need is a Chinese-English dictionary（汉英词典）.","eng":"What we need is a Chinese-English dictionary.","zh-cn":"我们需要的是一本汉英字典。"},{"eng-help":"He watched（监视） me more carefully than ever.","eng":"He watched me more carefully than ever.","zh-cn":"他比以往更加严格地监视我了。"},{"eng-help":"I'll tell you what to do next.","eng":"I'll tell you what to do next.","zh-cn":"我会告诉你下一步做什么。"},{"eng-help":"I'll tell you when to stop working.","eng":"I'll tell you when to stop working.","zh-cn":"我会告诉你何时停止工作。"},{"eng-help":"I've changed my mind.","eng":"I've changed my mind.","zh-cn":"我改主意了。"},{"eng-help":"She's free on Friday and Saturday.","eng":"She's free on Friday and Saturday.","zh-cn":"她周五和周六有空。 "},{"eng-help":"May I have a look（看一看） at the watch?","eng":"May I have a look at the watch?","zh-cn":"我能看看这块表吗？"},{"eng-help":"May I have a look at your pictures?","eng":"May I have a look at your pictures?","zh-cn":"我能看看你的照片吗？"},{"eng-help":"He told me that he had bought（买了） a book.","eng":"He told me that he had bought a book.","zh-cn":"他告诉我他买了一本书。"},{"eng-help":"At first（起初） the phone was busy（电话占线）.","eng":"At first the phone was busy.","zh-cn":"刚开始电话占线。"},{"eng-help":"Because there was heavy traffic（交通拥挤）, we were ten minutes late.","eng":"Because there was heavy traffic, we were ten minutes late.","zh-cn":"由于交通拥挤，我们晚了十分钟。"},{"eng-help":"But it looks like（看起来像） something I would never buy（绝不会买）.","eng":"But it looks like something I would never buy.","zh-cn":"但它看起来就像是我绝不会买的东西。"},{"eng-help":"China is the largest developing country in the world.","eng":"China is the largest developing country in the world.","zh-cn":"中国是世界上最大的发展中国家。"},{"eng-help":"Did you get that E-mail from me?（收到）","eng":"Did you get that E-mail from me?","zh-cn":"你收到我发的那封电子邮件了吗?"},{"eng-help":"Do you know where the post office is?","eng":"Do you know where the post office is?","zh-cn":"你知道邮局在哪儿吗？"},{"eng-help":"Few（几乎没有人）of them believe you.","eng":"Few of them believe you.","zh-cn":"他们当中几乎没人相信你。"},{"eng-help":"Hi. What can I get for your dinner(晚餐), sir?","eng":"Hi. What can I get for your dinner, sir?","zh-cn":"你好，先生。你晚餐要吃什么？"},{"eng-help":"I came here at least（至少） once a month（一月一次）.","eng":"I came here at least once a month.","zh-cn":"我一个月至少来这里一次。"},{"eng-help":"I don't know who broke the window.","eng":"I don't know who broke the window.","zh-cn":"我不知道谁打破了窗户。"},{"eng-help":"I don't want to spend five dollars on（花五美元买） a cup of water.","eng":"I don't want to spend five dollars on a cup of water.","zh-cn":"我不想花五美元买一瓶水。"},{"eng-help":"It is known to all that exercises（锻炼0 are good for（有益于） health（健康）.","eng":"It is known to all that exercises are good for health.","zh-cn":"众所周知，锻炼有益于健康。"},{"eng-help":"I'm going to run for the bus（赶公车） as soon as the bell rings（铃响）.","eng":"I'm going to run for the bus as soon as the bell rings.","zh-cn":"铃声一响，我就要去赶公共汽车。"},{"eng-help":"I looked for（寻找） the book on the desk but it was nowhere（不在那儿）.","eng":"I looked for the book on the desk but it was nowhere.","zh-cn":"我在桌上找了那本书，但是那本书不在那儿。"},{"eng-help":"I need to buy some 50-cent stamps（50美分的邮票）.","eng":"I need to buy some 50-cent stamps.","zh-cn":"我需要买一些50美分的邮票。"},{"eng-help":"I'll have to try using（试着用） the search engines（搜索引擎）.","eng":"I'll have to try using the search engines.","zh-cn":"我得试着用这个搜索引擎。"},{"eng-help":"It's easy to say, but difficult to do.","eng":"It's easy to say, but difficult to do.","zh-cn":"说起来容易，但做起来难。"},{"eng-help":"It's not much different from that heavy metal music（重金属音乐） you like so much.","eng":"It's not much different from that heavy metal music you like so much.","zh-cn":"那跟你很喜欢的重金属音乐并没有什么不同。"},{"eng-help":"It's their duty（责任） to look after（照看） these young trees.","eng":"It's their duty to look after these young trees.","zh-cn":"他们的责任是照看这些小树。"},{"eng-help":"I've been so busy（一直很忙） lately（最近），I haven't had time to call anybody.","eng":"I've been so busy lately，I haven't had time to call anybody.","zh-cn":"我最近一直很忙，所以没时间打电话给任何人。"},{"eng-help":"I've never heard such（这样） good music in a computer game.","eng":"I've never heard such good music in a computer game.","zh-cn":"我在电脑游戏里从没有听过这么好的音乐。"},{"eng-help":"Just because（仅仅因为)you like, it doesn't mean（并不意味着...） I will.","eng":"Just because you like, it doesn't mean I will.","zh-cn":"你喜欢它，并不意味我也会喜欢。"},{"eng-help":"Let me type in（输入） your address（地址） and then open the account（开户）.","eng":"Let me type in your address and then open the account.","zh-cn":"我先输入你的住址，然后开户。"},{"eng-help":"Let's sit over here（坐在一边） until it's time for you to board（登机）.","eng":"Let's sit over here until it's time for you to board.","zh-cn":"我们就在这里坐到你上飞机的时间。"},{"eng-help":"Li Bai is one of（之一）the greatest poets（最伟大的诗人） that have ever lived（有史以来）in China.","eng":"Li Bai is one of the greatest poets that have ever lived in China.","zh-cn":"李白是中国有史以来最伟大的诗人之一。"},{"eng-help":"MR. Lin assigned too much homework today.","eng":"MR. Lin assigned too much homework today.","zh-cn":"今天林先生（老师）留的家庭作业太多了。"},{"eng-help":"Not all Americans are interested in sports, of course.","eng":"Not all Americans are interested in sports, of course.","zh-cn":"当然， 并非所有美国人都对运动赶兴趣。"},{"eng-help":"Of course they did not always（不总是） have money to buy the things they wanted.","eng":"Of course they did not always have money to buy the things they wanted.","zh-cn":"当然他们不总是有钱去买他们想要的东西。"},{"eng-help":"Okay, and I need a pen to fill out the address（填写地址）.","eng":"Okay, and I need a pen to fill out the address.","zh-cn":"好，我还得借一支笔来填写地址。"},{"eng-help":"Once all the information（信息，内容） is completed（完成）, you sign（签名） here.","eng":"Once all the information is completed, you sign here.","zh-cn":"所有内容都填完之后，你就在这儿签名。"},{"eng-help":"She begins cooking（做饭） as soon as she gets home.","eng":"She begins cooking as soon as she gets home.","zh-cn":"她每天一到家就开始做饭。"},{"eng-help":"She looks lovely（看起来可爱）when she is happy.","eng":"She looks lovely when she is happy.","zh-cn":"她高兴的时候看起来很可爱。"},{"eng-help":"Yeah, but they play the same songs（播放同一首歌） over and over again（一遍又一遍）.","eng":"Yeah, but they play the same songs over and over again.","zh-cn":"没错，但他们总重复播放同一首歌。"},{"eng-help":"Yeah, I'm anxious（着急） to hear all the songs from her new album（新唱片）.","eng":"Yeah, I'm anxious to hear all the songs from her new album.","zh-cn":"是的，我也很急着听她新唱片里所有的歌。"},{"eng-help":"Some football teams（足球队） will have games（比赛） there.","eng":"Some football teams will have games there.","zh-cn":"有几支足球队要在那里进行比赛。"},{"eng-help":"You are clever enough（足够聪明） to pass the exam.","eng":"You are clever enough to pass the exam.","zh-cn":"你很聪明，能通过考试。"},{"eng-help":"You can buy water inside（在里面）.","eng":"You can buy water inside.","zh-cn":"你可以在里面买水喝。"},{"eng-help":"You'd better（最好） go quickly（快走） before I change my mind（在我改主意之前）.","eng":"You'd better go quickly before I change my mind.","zh-cn":"趁着我还没有改变主意，你赶快走吧。"},{"eng-help":"He borrowed some money（借钱） from his friend and hurried（匆匆赶去） there.","eng":"He borrowed some money from his friend and hurried there.","zh-cn":"他从朋友那借了些钱，便急匆匆地赶去那儿。"},{"eng-help":"Neither the land nor the weather is good for farming（耕作）. （neither… nor… 既不… 也不...）","eng":"Neither the land nor the weather is good for farming.","zh-cn":"这儿的土地和气候都不适宜耕作。"},{"eng-help":"On that flight(航班) were a mother and her four-year-old son, whose name（他的名字） was Jeffrey.","eng":"On that flight were a mother and her four-year-old son, whose name was Jeffrey.","zh-cn":"在那次航班上，有一位妈妈和她四岁的儿子，儿子名叫杰佛瑞。"},{"eng-help":"Air is all around（围绕） us.","eng":"Air is all around us.","zh-cn":"空气围绕在我们周围。"},{"eng-help":"This street will be widened（拓宽）.","eng":"This street will be widened.","zh-cn":"这条大街将要加宽。"},{"eng-help":"This TV channel（电视频道） has too many commercials（广告）.","eng":"This TV channel has too many commercials.","zh-cn":"这个电视台频道广告太多了。"},{"eng-help":"Why（怎么了）, do you want me to change the channel（换台）?","eng":"Why, do you want me to change the channel?","zh-cn":"怎么了，你要我换个频道吗？"},{"eng-help":"We are going to have an English party today.","eng":"We are going to have an English party today.","zh-cn":" 今天我们将有一个英语聚会。"},{"eng-help":"We're running behind schedule（落后时刻表，误点） by about 15 minutes.","eng":"We're running behind schedule by about 15 minutes.","zh-cn":"我们大约误点十五分钟。"},{"eng-help":"Could you tell me how to go to（怎么去） your university?","eng":"Could you tell me how to go to your university?","zh-cn":"请你告诉我如何去你的大学，可以吗？"},{"eng-help":"He knew the time of the meeting.","eng":"He knew the time of the meeting.","zh-cn":"他知道开会时间。"},{"eng-help":"I didn't sleep well.","eng":"I didn't sleep well.","zh-cn":"我没睡好。 "},{"eng-help":"I had lunch with a friend of mine.","eng":"I had lunch with a friend of mine.","zh-cn":"我和我的一个朋友一块吃了午饭。"},{"eng-help":"I heard she got married last week.","eng":"I heard she got married last week.","zh-cn":"我听说她上星期结婚了。"},{"eng-help":"I take a walk（散步） in the early morning（一大早） every day.","eng":"I take a walk in the early morning every day.","zh-cn":"我每天一大早出去散步。  "},{"eng-help":"I watched TV（看电视） all night（整夜） yesterday.","eng":"I watched TV all night yesterday.","zh-cn":"我昨天看了一夜电视。"},{"eng-help":"The question is too difficult to answer.","eng":"The question is too difficult to answer.","zh-cn":"这个问题太难回答。"},{"eng-help":"What is the color of your new computer?","eng":"What is the color of your new computer?","zh-cn":"你的新电脑是什么颜色？"},{"eng-help":"When I arrived at the station, the train had already left（开走了）.","eng":"When I arrived at the station, the train had already left.","zh-cn":"我到车站时，火车已经开了。 "},{"eng-help":"I'll phone（打电话） you again when I get home.","eng":"I'll phone you again when I get home.","zh-cn":"回家后，我将再给你打电话。"},{"eng-help":"It is a fine（晴朗的） spring day.","eng":"It is a fine spring day.","zh-cn":"那是一个晴朗的春天。"},{"eng-help":"I heard（听说） it said that this factory(工厂) was founded（创建） in 1901.","eng":"I heard it said that this factory was founded in 1901.","zh-cn":"我听说这家工厂是1901年创建的。"},{"eng-help":"I think a person's character（性格） is more important than his appearance（外表）.","eng":"I think a person's character is more important than his appearance.","zh-cn":"我认为一个人的性格比他的外表更重要。"},{"eng-help":"After I get home, I'll call you.","eng":"After I get home, I'll call you.","zh-cn":"到家以后，我会给你打电话。"},{"eng-help":"Call me tomorrow if you have time.","eng":"Call me tomorrow if you have time.","zh-cn":"你明天要有时间就给我打个电话。 "},{"eng-help":"Could you show me the way to（指给我去… 的路） the train station（火车站）?","eng":"Could you show me the way to the train station?","zh-cn":"你可以告诉我去火车站的路吗？"},{"eng-help":"Could you tell me how to get to（如何去） the bank（银行）?","eng":"Could you tell me how to get to the bank?","zh-cn":"你可以告诉我如何去银行吗？"},{"eng-help":"Do you have anything to say?","eng":"Do you have anything to say?","zh-cn":"你有什么要说的吗？"},{"eng-help":"Does the computer have instructions（使用说明） on it?","eng":"Does the computer have instructions on it?","zh-cn":"电脑上面有使用说明吗？"},{"eng-help":"Don't you think smoking is harmful（有害的） to your health?","eng":"Don't you think smoking is harmful to your health?","zh-cn":"你不认为抽烟对你的身体有害吗？"},{"eng-help":"Give us a few more minutes（多几分钟） to decide（决定）.","eng":"Give us a few more minutes to decide.","zh-cn":"再给我们几分钟时间做决定。"},{"eng-help":"How long will it take（花多长时间） us to get there?","eng":"How long will it take us to get there?","zh-cn":"我们到那儿要用多长时间？"},{"eng-help":"I am very much interested in sports.","eng":"I am very much interested in sports.","zh-cn":"我对运动很感兴趣。"},{"eng-help":"I can't finish（完成） the task（任务） within（在...之内） 1 hour.","eng":"I can't finish the task within 1 hour.","zh-cn":"1小时内我不能完成这个任务。"},{"eng-help":"I don't feel very well.   （feel well 指身体舒适，本句为否定句）","eng":"I don't feel very well.","zh-cn":"我觉得不太舒服。"},{"eng-help":"I don't think he has time to play chess(下棋) with you.","eng":"I don't think he has time to play chess with you.","zh-cn":"我想他没有时间和你下棋。"},{"eng-help":"I enjoyed this meal thoroughly（非常地，完全地）.","eng":"I enjoyed this meal thoroughly.","zh-cn":"我非常喜欢这顿饭。"},{"eng-help":"I have no interest in（不感兴趣） what they say（他们说的话） about me（关于我）.","eng":"I have no interest in what they say about me.","zh-cn":"我对别人怎么说我不感兴趣。"},{"eng-help":"I lost my keys（丢失钥匙） on my way to work.","eng":"I lost my keys on my way to work.","zh-cn":"在去上班的路上，我的钥匙丢了。"},{"eng-help":"I need you to fill out（填写） the top portion（上半部分） of the form（表格） first（首先）.","eng":"I need you to fill out the top portion of the form first.","zh-cn":"我需要你先填写表格的上半部。"},{"eng-help":"I'll call you as soon as（一... 就...） I arrive.","eng":"I'll call you as soon as I arrive.","zh-cn":"我一到就给你打电话。"},{"eng-help":"I'm now a distance education(远程教育) student.","eng":"I'm now a distance education student.","zh-cn":"我现在是一名远程教育学员。"},{"eng-help":"It takes a lot of time（花时间）to learn a foreign language（外语）.","eng":"It takes a lot of time to learn a foreign language.","zh-cn":"学习一门外语需要很多时间。"},{"eng-help":"It was a hard job, but he did not mind（不在乎）.","eng":"It was a hard job, but he did not mind.","zh-cn":"这项工作很艰苦，但他并不在乎。"},{"eng-help":"Jane moved to New York（搬到纽约） at the end of last month（上个月）.","eng":"Jane moved to New York at the end of last month.","zh-cn":"简在上月底就移居纽约了。"},{"eng-help":"Jenny is fond of reading and often borrows books from the city library.","eng":"Jenny is fond of reading and often borrows books from the city library.","zh-cn":"珍妮喜爱读书，经常从市图书馆借回许多书。"},{"eng-help":"Let's try（尝试） something different（不同的口味/不同的东西）.","eng":"Let's try something different.","zh-cn":"让我们尝尝其它不同的口味。"},{"eng-help":"Lincoln was the US president who set the slaves free（解放奴隶）.","eng":"Lincoln was the US president who set the slaves free.","zh-cn":"林肯是解放黑奴的那位美国总统。"},{"eng-help":"One can never succeed（成功） without enough confidence（信心） in himself.","eng":"One can never succeed without enough confidence in himself.","zh-cn":"一个人如果对自己没有足够的信心就不可能成功。"},{"eng-help":"Our teacher always encourages us to speak English bravely（勇敢地）. ","eng":"Our teacher always encourages us to speak English bravely. ","zh-cn":"我们的老师总是鼓励我们大胆讲英语。"},{"eng-help":"People all over the world（全世界） are trying to help（尽力帮助） the people in Sichuan.","eng":"People all over the world are trying to help the people in Sichuan.","zh-cn":"全世界人民都在尽力帮助四川人民。"},{"eng-help":"Please get off（下车） the bus at the next stop（下一站）.","eng":"Please get off the bus at the next stop.","zh-cn":"请在下一站下车。"},{"eng-help":"Please tell me what to do next（接下来）.","eng":"Please tell me what to do next.","zh-cn":"请告诉我接下来做什么。"},{"eng-help":"Please tell me where I can find（在哪儿能找到） these books.","eng":"Please tell me where I can find these books.","zh-cn":"请告诉我在哪儿能找到这些书。"},{"eng-help":"The girls are busy making preparations（忙着准备） for the weekend party.","eng":"The girls are busy making preparations for the weekend party.","zh-cn":"姑娘们忙着准备周末的晚会。"},{"eng-help":"The little boy wanted to exchange（交换） his toy car（玩具汽车） for my cake.  （exchange A for B， 用A交换B）","eng":"The little boy wanted to exchange his toy car for my cake.","zh-cn":"这个小男孩想用他的玩具小汽车换我的蛋糕。"},{"eng-help":"The more I study(学习), the less I seem to learn（学会的东西）. （the more… the less, …越多… 越少）","eng":"The more I study, the less I seem to learn.","zh-cn":"我读的书越多，好象学到的东西越少。"},{"eng-help":"The price(价格) of gasoline（汽油） will come down（下降） in Chinese market（市场）.","eng":"The price of gasoline will come down in Chinese market.","zh-cn":"中国市场上的汽油价将会下降。"},{"eng-help":"The teacher told the students to sit down quietly（安静地）.","eng":"The teacher told the students to sit down quietly.","zh-cn":"老师叫学生们安静入座。"},{"eng-help":"There's a party at my house this Friday.","eng":"There's a party at my house this Friday.","zh-cn":"这星期五在我们家有个宴会。"},{"eng-help":"These five boys failed（不及格） in their English exam last term（上学期）.","eng":"These five boys failed in their English exam last term.","zh-cn":"这五个男同学上学期的英语考试没过。"},{"eng-help":"They have been working together（在一起工作） for three months and have come to know each other（相互了解） better（更好）.","eng":"They have been working together for three months and have come to know each other better.","zh-cn":"他们一起工作了三个月，相互之间也更了解了。"},{"eng-help":"This is the worst hospital（最差医院） I have ever seen（曾经见过的）. ","eng":"This is the worst hospital I have ever seen. ","zh-cn":"这是我见过的最差的医院。"},{"eng-help":"This one's pretty（漂亮）, but it costs too much（太贵）.","eng":"This one's pretty, but it costs too much.","zh-cn":"这个很漂亮，但是很贵。"},{"eng-help":"This pair（双） of shoes cost（花费） me 260 yuan.","eng":"This pair of shoes cost me 260 yuan.","zh-cn":"这双鞋花了我260元钱。"},{"eng-help":"We believe that Peter will be given the prize（获奖）.","eng":"We believe that Peter will be given the prize.","zh-cn":"我们相信彼得将获得奖金。"},{"eng-help":"We have made little progress（没取得什么进展）. （little 为否定意义）","eng":"We have made little progress.","zh-cn":"我们没有取得什么进展。"},{"eng-help":"What are you doing next weekend?","eng":"What are you doing next weekend?","zh-cn":"下周末你干什么？"},{"eng-help":"What can I do for you?","eng":"What can I do for you?","zh-cn":"我能为您做点什么？"},{"eng-help":"What time does the dining room（餐厅） open?","eng":"What time does the dining room open?","zh-cn":"餐厅几点营业？"},{"eng-help":"What would you like（想要） to drink（喝）, mineral water（矿泉水） or tea（茶）?","eng":"What would you like to drink, mineral water or tea?","zh-cn":"你喜欢喝点什么，矿泉水还是茶水？"},{"eng-help":"Would you please（请帮忙） open the door for me?","eng":"Would you please open the door for me?","zh-cn":"请你帮忙打开门好吗？ "},{"eng-help":"You must always remember not to cheat（作弊） in exams.","eng":"You must always remember not to cheat in exams.","zh-cn":"你们必须记住永远都不要在考试时作弊。"},{"eng-help":"Mr. Green was frightened（吓住了） by the message（信息，消息）.","eng":"Mr. Green was frightened by the message.","zh-cn":"格林先生被这消息吓住了。"},{"eng-help":"The reason he was late was that he missed（错过，没赶上） the bus.","eng":"The reason he was late was that he missed the bus.","zh-cn":"他迟到的原因是误了车。"},{"eng-help":"Can you finish your work in time（及时）?","eng":"Can you finish your work in time?","zh-cn":"你能及时完成工作吗？"},{"eng-help":"David will get a birthday gift（生日礼物） from his sister.","eng":"David will get a birthday gift from his sister.","zh-cn":"戴维将从他的姐姐那里得到一份生日礼物。"},{"eng-help":"We have known each other for 6 years.","eng":"We have known each other for 6 years.","zh-cn":"我们认识有六年了。"},{"eng-help":"When did you get up yesterday morning?","eng":"When did you get up yesterday morning?","zh-cn":"你昨天早上几点起的床？"},{"eng-help":"Men differ from（与…不同，区别于） other animals in that（在于） they can speak and think.","eng":"Men differ from other animals in that they can speak and think.","zh-cn":"人和其他动物的区别在于他们有语言和思想。"},{"eng-help":"Americans usually eat three meals（吃三餐） a day.","eng":"Americans usually eat three meals a day.","zh-cn":"美国人通常一日三餐。"},{"eng-help":"When will he come to see you?","eng":"When will he come to see you?","zh-cn":"他什么时候会来看你？ "},{"eng-help":"You don't need to buy that.","eng":"You don't need to buy that.","zh-cn":"你没必要买这东西。"},{"eng-help":"He translated the famous novel（著名小说） into English（翻译成了英文）.","eng":"He translated the famous novel into English.","zh-cn":"他把那部著名的小说译成了英文。"},{"eng-help":"How are you feeling（感觉怎样） today?","eng":"How are you feeling today?","zh-cn":"你今天感觉怎么样？"},{"eng-help":"I get to my office at 10:30.","eng":"I get to my office at 10:30.","zh-cn":"我十点半到办公室。"},{"eng-help":"I hope you'll be well（恢复健康） soon.","eng":"I hope you'll be well soon.","zh-cn":"祝你早日恢复健康。"},{"eng-help":"I think you're wrong（错的）.","eng":"I think you're wrong.","zh-cn":"我认为你是错的。"},{"eng-help":"I am not satisfied with his answer.","eng":"I am not satisfied with his answer.","zh-cn":"我对他的回答不满意。"},{"eng-help":"You shouldn't smoke（抽烟） too much.","eng":"You shouldn't smoke too much.","zh-cn":"你不该抽那么多的烟。"},{"eng-help":"He can run as fast as（跑得和…一样快） Jim.","eng":"He can run as fast as Jim.","zh-cn":"他跑的和吉姆一样快。 "},{"eng-help":"They seem to（看起来） have more activities at night than in the day.","eng":"They seem to have more activities at night than in the day.","zh-cn":"他们看起来晚上的活动比白天多。"},{"eng-help":"It rained every day while（当...时候） I was in Shanghai.","eng":"It rained every day while I was in Shanghai.","zh-cn":"我在上海时，天天下雨。"},{"eng-help":"My parents got married in 1954.","eng":"My parents got married in 1954.","zh-cn":"我父母是1954年结婚的。"},{"eng-help":"Please give me a glass of beer(一杯啤酒).","eng":"Please give me a glass of beer.","zh-cn":"请给我来杯啤酒。"},{"eng-help":"What's your plan（计划） for the summer holiday（暑假）?","eng":"What's your plan for the summer holiday?","zh-cn":"暑假你计划干什么？"},{"eng-help":"How long（多久） has she lived there?","eng":"How long has she lived there?","zh-cn":"她在那儿住多久了？ "},{"eng-help":"The movie began when we got there.","eng":"The movie began when we got there.","zh-cn":"我们一到那儿电影就开演了。"},{"eng-help":"Don't tell me what to do!","eng":"Don't tell me what to do!","zh-cn":"用不着告诉我该做什么！"},{"eng-help":"How long do you watch TV every day?","eng":"How long do you watch TV every day?","zh-cn":"你每天看多长时间电视？ "},{"eng-help":"How wide（多宽） is this street?","eng":"How wide is this street?","zh-cn":"这条街有多宽？"},{"eng-help":"How（怎样） will you spend（度过） the evening?","eng":"How will you spend the evening?","zh-cn":"你晚上干什么？"},{"eng-help":"I buy some food on my way home（在回家路上）.","eng":"I buy some food on my way home.","zh-cn":"我在回家路上买些吃的。"},{"eng-help":"I'll stay at home and watch TV.","eng":"I'll stay at home and watch TV.","zh-cn":"我将呆在家看电视。"},{"eng-help":"I'm telling the truth.","eng":"I'm telling the truth.","zh-cn":"我说的是事实。"},{"eng-help":"Let's go dancing tonight.","eng":"Let's go dancing tonight.","zh-cn":"今晚我们去跳舞吧。"},{"eng-help":"She was late for（迟到） work again.","eng":"She was late for work again.","zh-cn":"她上班又迟到了。"},{"eng-help":"Tom's father has taught English here since（从…之后） he graduated（毕业）from Peking University（北大）.","eng":"Tom's father has taught English here since he graduated from Peking University.","zh-cn":"汤姆的父亲从北京大学毕业后就一直在这里教英语。"},{"eng-help":"What do you plan（计划0 to do this Friday?","eng":"What do you plan to do this Friday?","zh-cn":"星期五你打算干什么？ "},{"eng-help":"What do you want for dinner?","eng":"What do you want for dinner?","zh-cn":"晚饭你想吃什么？"},{"eng-help":"What's the size（尺码） of your shoes?","eng":"What's the size of your shoes?","zh-cn":"你的鞋多大尺寸？"},{"eng-help":"Do you feel better（感觉好点了） now?","eng":"Do you feel better now?","zh-cn":"你现在觉得好点了吗？"},{"eng-help":"Where did you go for dinner（去吃饭） yesterday?","eng":"Where did you go for dinner yesterday?","zh-cn":"你们昨天去哪儿吃饭了？ "},{"eng-help":"You should（应该） arrive at the airport at 10 am.","eng":"You should arrive at the airport at 10 am.","zh-cn":"你该在上午10点到机场。"},{"eng-help":"You should stop smoking（戒烟）.","eng":"You should stop smoking.","zh-cn":"你应该戒烟。"},{"eng-help":"I finished my work at 6 pm（下午6点）.","eng":"I finished my work at 6 pm.","zh-cn":"下午6点我干完了活。"},{"eng-help":"I found（捡到） $10 on the street.","eng":"I found $10 on the street.","zh-cn":"我在大街上捡到10美元。"},{"eng-help":"When did you buy the car?","eng":"When did you buy the car?","zh-cn":"你什么时候买的车？"},{"eng-help":"Do you like traveling?","eng":"Do you like traveling?","zh-cn":"你喜欢旅游吗？"},{"eng-help":"What do you think about it?","eng":"What do you think about it?","zh-cn":"你对此事怎么看？"},{"eng-help":"What's the color of your new dress（衣服）?","eng":"What's the color of your new dress?","zh-cn":"你的新衣服是什么颜色的？ "},{"eng-help":"He was not just（不仅是） an actor, but also a writer, producer and director.  ","eng":"He was not just an actor, but also a writer, producer and director.  ","zh-cn":"他不仅是一名演演员，还是一位作家，制片人和导演。  "},{"eng-help":"You can see many interesting things on your way（在路上）.  ","eng":"You can see many interesting things on your way.    ","zh-cn":"一路上你能看见许多有趣的事情。  "},{"eng-help":"Once, however, the clock failed（没能） to give the correct（正确的） time.","eng":"Once, however, the clock failed to give the correct time.  ","zh-cn":"然而，这座钟曾有一次没有报出准确的时间。  "},{"eng-help":"He was too tired to（太累而不） say a word and went to bed at once.","eng":"He was too tired to say a word and went to bed at once.  ","zh-cn":"他太累了，一句话也没说就立刻上床睡觉去了。  "},{"eng-help":"Rain water finds its way to（流入） streams and rivers.","eng":"Rain water finds its way to streams and rivers.  ","zh-cn":"雨水流进江河。  "},{"eng-help":"The White House is more than （不仅是）a home; it has offices（办公室） for the President.","eng":"The White House is more than a home; it has offices for the President.","zh-cn":"白宫不仅是住所，还是总统办公的官邸。"},{"eng-help":"Years ago, zoos kept all kinds of animals in small cages（关在小笼子里）.","eng":"Years ago, zoos kept all kinds of animals in small cages.","zh-cn":"多年以前，动物园把各种动物都关在小笼子里。"},{"eng-help":"Also you can carry（带着）many things with you in a car.","eng":"Also you can carry many things with you in a car.","zh-cn":"而且你能带很多东西放在车上。"},{"eng-help":"Li Mei studies in a university far away from（远离） her home.","eng":"Li Mei studies in a university far away from her home.  ","zh-cn":"李梅在远离自己家的一所大学学习。  "},{"eng-help":"A few minutes later the doctor came out（出来） of his room for the third time（第三次）.  ","eng":"A few minutes later the doctor came out of his room for the third time.  ","zh-cn":"几分钟后，医生第三次从他的房间走出来。  "},{"eng-help":"A man and his wife had a small restaurant（饭店） near a station（车站）.  ","eng":"A man and his wife had a small restaurant near a station.  ","zh-cn":"一个男人和他的妻子在靠近车站的地方开了家小饭馆。  "},{"eng-help":"Your reading purpose（目的） is quite different; it is to understand meaning（理解意思）.  ","eng":"Your reading purpose is quite different; it is to understand meaning.  ","zh-cn":"你阅读目的大不相同，是为了理解意思。  "},{"eng-help":"Zoos help to protect（保护） all kinds of animals.  ","eng":"Zoos help to protect all kinds of animals.  ","zh-cn":"动物园帮助保护了各种动物。  "},{"eng-help":"I have two coats（上衣、外套）. One is red, the other is blue.","eng":"I have two coats. One is red, the other is blue.  ","zh-cn":"我有两件上衣。一件红的，另一件是蓝色的。  "},{"eng-help":"New York University has forty-eight thousand（四万八千） students.","eng":"New York University has forty-eight thousand students.  ","zh-cn":"纽约大学有学生四万八千名。  "},{"eng-help":"On the last day of the old year, there is a family dinner（家宴）.","eng":"On the last day of the old year, there is a family dinner.  ","zh-cn":"在即将过去的一年的最后一天，会有一次家庭聚餐。  "},{"eng-help":"Many people don’t listen to music and they can get along quite well（活得很好）.","eng":"Many people don’t listen to music and they can get along quite well.  ","zh-cn":"许多人不听音乐，却生活得相当好。  "},{"eng-help":"All things are difficult before they are easy （先难后易，由难而易）.","eng":"All things are difficult before they are easy.","zh-cn":"凡事总是由难而易。"},{"eng-help":"He told me that he had lived in America for ten years before he came to China.","eng":"He told me that he had lived in America for ten years before he came to China.","zh-cn":"他告诉我在来中国前他在美国已生活十年了。"},{"eng-help":"The plan has been over fulfilled（超额完成） by now.","eng":"The plan has been over fulfilled by now.","zh-cn":"目前，已经超额完成计划。"},{"eng-help":"Wang Li's father has taught English here since（自从） he has graduated（毕业） from Peking University.","eng":"Wang Li's father has taught English here since he has graduated from Peking University.","zh-cn":"王丽的父亲从北京大学毕业后就一直在这里教英语。"},{"eng-help":"Karsh was praised as a master portraitist（人像大师）, often working in black and white, influenced by（受…影响） great painters of the past.","eng":"Karsh was praised as a master portraitist, often working in black and white, influenced by great painters of the past.","zh-cn":"卡什被人们誉为人像大师，由于受到历史上一些著名画家的影响，他在拍摄中经常运用黑白摄影。"},{"eng-help":"Of course, it has special meaning for people like me（像我这样的人）.  ","eng":"Of course, it has special meaning for people like me.  ","zh-cn":"当然，它对我这样的人具有特殊的含义。  "},{"eng-help":"The bus is coming. Let’s get on the bus.  ","eng":"The bus is coming. Let’s get on the bus.  ","zh-cn":"那辆公交车来了。我们上车。  "},{"eng-help":"Some women have managed（努力）to become doctors.","eng":"Some women have managed to become doctors.","zh-cn":"有些妇女经努力当上了医生。"},{"eng-help":"It is known to all that light travels in straight lines（直线）.","eng":"It is known to all that light travels in straight lines.","zh-cn":"众所周知，光以直线运行。"},{"eng-help":"It seems（似乎） that you know someone cares about（在乎，关心） you.","eng":"It seems that you know someone cares about you.","zh-cn":"你似乎知道有人在关心你。"},{"eng-help":"My mother says she really likes these styles（这些式样） and so do I（我也是）.","eng":"My mother says she really likes these styles and so do I.","zh-cn":"我妈妈说她很喜欢这些式样，我也很喜欢。"},{"eng-help":"What time（何时） shall we leave（离开）?","eng":"What time shall we leave?","zh-cn":"我们何时出发？"},{"eng-help":"Are you willing（愿意） to give more money to the poor people?  ","eng":"Are you willing to give more money to the poor people?  ","zh-cn":"你愿意给穷人更多的钱吗？   "},{"eng-help":"Did you go fishing（去钓鱼） with your friends last Sunday?  ","eng":"Did you go fishing with your friends last Sunday?  ","zh-cn":"上周日你与你的朋友去钓鱼了？    "},{"eng-help":"Do you mind my smoking here?  ","eng":"Do you mind my smoking here?  ","zh-cn":"我在这里吸烟，你介意吗？   "},{"eng-help":"Do you still have anything that you don't understand?   ","eng":"Do you still have anything that you don't understand?   ","zh-cn":"你还有什么不懂的吗?   "},{"eng-help":"He asks me to stay in the room till（直到） he comes back.  ","eng":"He asks me to stay in the room till he comes back.  ","zh-cn":"他让我呆在屋里等他回来。   "},{"eng-help":"He doesn't like the way（方式，样子） you talk to him.  ","eng":"He doesn't like the way you talk to him.  ","zh-cn":"他不喜欢你和他说话的样子。   "},{"eng-help":"He's growing more（长得更像，越长越像） like his father now.  ","eng":"He's growing more like his father now.  ","zh-cn":"他现在越长越像他的父亲。   "},{"eng-help":"I didn't hear what you said to him.  ","eng":"I didn't hear what you said to him.  ","zh-cn":"我没听见你对他说的是什么.   "},{"eng-help":"I happened（碰巧，刚好） to pass by （路过） when the boy fell into the river.  ","eng":"I happened to pass by when the boy fell into the river.  ","zh-cn":"那个小孩掉到/落入河里的时候我正好经过那里。  "},{"eng-help":"I have no idea what to say.  ","eng":"I have no idea what to say.  ","zh-cn":"我不知道说什么好。   "},{"eng-help":"I promise you that I will do everything I can（尽一切努力） to help him.  ","eng":"I promise you that I will do everything I can to help him.  ","zh-cn":"我保证尽一切努力帮助他。  "},{"eng-help":"I will make sure（确保） that every student has a copy of the book.  ","eng":"I will make sure that every student has a copy of the book.  ","zh-cn":"我一定会确保每个学生都有一本这种书。  "},{"eng-help":"I'll be waiting for you at the library this time tomorrow.  ","eng":"I'll be waiting for you at the library this time tomorrow.  ","zh-cn":"我明天这时候在图书馆等你。   "},{"eng-help":"I'm sure we'll have a good time.  ","eng":"I'm sure we'll have a good time.  ","zh-cn":"我相信我们会很开心的。   "},{"eng-help":"I've got used to（适应，习惯于） the weather（天气） in Shanghai.  ","eng":"I've got used to the weather in Shanghai.  ","zh-cn":"我已经适应了上海的这种天气。   "},{"eng-help":"Many young people want to be volunteers（志愿者） for Olympic Games.  ","eng":"Many young people want to be volunteers for Olympic Games.  ","zh-cn":"许多年轻人想做奥运会的志愿者。   "},{"eng-help":"People usually hate mice（憎恨老鼠）, but one mouse（一只老鼠） won the hearts of the people all over the world.  ","eng":"People usually hate mice, but one mouse won the hearts of the people all over the world.  ","zh-cn":"人们通常憎恶老鼠，但这只老鼠却赢得了全世界人们的心。   "},{"eng-help":"Talking for a long time makes me tired.  ","eng":"Talking for a long time makes me tired.  ","zh-cn":"长时间的谈话使我感到疲倦。   "},{"eng-help":"The earthquake in Sichuan made Chinese people unite（使团结） as one（像一个人）.  ","eng":"The earthquake in Sichuan made Chinese people unite as one.  ","zh-cn":"发生在四川的地震使全中国人民团结的像一个人似的。   "},{"eng-help":"The majority（大多数） of us agree with you.  ","eng":"The majority of us agree with you.  ","zh-cn":"我们绝大多数与你意见一致。   "},{"eng-help":"The man-made satellite (人造卫星) made by China is working very well.  ","eng":"The man-made satellite (卫星) made by China is working very well.  ","zh-cn":"中国制造的卫星运行良好。   "},{"eng-help":"The prices of vegetables are different from place to place（各地不同）.  ","eng":"The prices of vegetables are different from place to place.  ","zh-cn":"蔬菜价格地区间是有差别的。   "},{"eng-help":"The teacher came earlier than expected (预期).  ","eng":"The teacher came earlier than expected (预期).  ","zh-cn":"老师来得比预期的早。   "},{"eng-help":"These two students from our school have been admitted to（被...录取） Beijing University.  ","eng":"These two students from our school have been admitted to Beijing University.  ","zh-cn":"我们学校这两个学生已经被北京大学录取了。   "},{"eng-help":"They were sitting in the park, playing cards.  ","eng":"They were sitting in the park, playing cards.  ","zh-cn":"他们坐在公园里打扑克。   "},{"eng-help":"Traveling by train is slower than by plane, but it has its advantages(优势).  ","eng":"Traveling by train is slower than by plane, but it has its advantages.  ","zh-cn":"乘火车旅行比乘飞机旅行慢，但有它的优势。   "},{"eng-help":"We must read as much as possible to enlarge（扩大） our vocabulary.  ","eng":"We must read as much as possible to enlarge our vocabulary.  ","zh-cn":"我们必须尽可能多的阅读以扩大我们的词汇量。   "},{"eng-help":"We should adapt ourselves to（使自己适应） the new surroundings（环境）.  ","eng":"We should adapt ourselves to the new surroundings.  ","zh-cn":"我们应当使自己适应新的环境。   "},{"eng-help":"Were there any phone calls for me while I was out?  ","eng":"Were there any phone calls for me while I was out?  ","zh-cn":"我不在时，有人打电话给我吗？   "},{"eng-help":"When she got home that evening, she showed her husband the beautiful hat she had bought.  ","eng":"When she got home that evening, she showed her husband the beautiful hat she had bought.  ","zh-cn":"那天傍晚她回家后，给丈夫看她买的那顶漂亮的帽子。   "},{"eng-help":"Would you mind waiting outside?  ","eng":"Would you mind waiting outside?  ","zh-cn":"请你在外面等，好吗？   "},{"eng-help":"Young persons under twenty-five make up（构成，占） nearly half of the American population.  ","eng":"Young persons under twenty-five make up nearly half of the American population.  ","zh-cn":"25岁以下的年轻人几乎占了美国人口的一半。   "},{"eng-help":"I think everyone knows how to swim（如何游泳）.","eng":"I think everyone knows how to swim.","zh-cn":"我想每个人都知道如何游泳。"},{"eng-help":"The man is easy to deal with（容易相处）.","eng":"The man is easy to deal with.","zh-cn":"这个人容易相处。"},{"eng-help":"Three hundred dollars is not enough to buy this typewriter.  ","eng":"Three hundred dollars is not enough to buy this typewriter.  ","zh-cn":"买这台打字机300美元不够。  "},{"eng-help":"We should keep it in mind（牢记） that our country is a developing（发展中） one.  ","eng":"We should keep it in mind that our country is a developing one.  ","zh-cn":"我们应牢记在心，我国是一个发展中的国家。  "},{"eng-help":"We are more convinced（相信） than ever that the reform and open-door policy（改革开放政策） is correct.                    more... than ever比以往更...... ","eng":"We are more convinced than ever that the reform and open-door policy is correct.   ","zh-cn":"我们比过去任何时候都坚信，改革开放的政策是正确的。  "},{"eng-help":"There is only some old furniture（家具） in the room.  ","eng":"There is only some old furniture in the room.  ","zh-cn":"这间房里布置了几件旧家具。  "},{"eng-help":"These goods are not up to standard(达到标准).  ","eng":"These goods are not up to standard.  ","zh-cn":"这些商品未达到标准。  "},{"eng-help":"The reason why he failed is that he relies on others（依赖别人） too much.  ","eng":"The reason why he failed is that he relies on others too much.  ","zh-cn":"他失败的原因是他太依赖别人。  "},{"eng-help":"The jury (陪审团) was composed of（由...组成） eight white people and four black people.  ","eng":"The jury (陪审团) was composed of eight white people and four black people.  ","zh-cn":"陪审团由八位白人和四位黑人组成。  "},{"eng-help":"The old man was knocked down（撞到） by a taxi when he was crossing the street.  ","eng":"The old man was knocked down by a taxi when he was crossing the street.  ","zh-cn":"老人在过马路时被一辆出租车撞倒了。  "},{"eng-help":"The pen that I'm writing with（写字用的） is a birthday present from my sister（姐姐送的礼物）.  ","eng":"The pen that I'm writing with is a birthday present from my sister.  ","zh-cn":"我正在用的这支笔是我姐姐送我的生日礼物。  "},{"eng-help":"What did I say to cause（导致） you to do that?  ","eng":"What did I say to cause you to do that?  ","zh-cn":"我说什么了使你这样做？  "},{"eng-help":"What is the color of your new computer?  ","eng":"What is the color of your new computer?  ","zh-cn":"你的新电脑是什么颜色？   "},{"eng-help":"When I just gone out of the building, it began to rain.  ","eng":"When I just gone out of the building, it began to rain.  ","zh-cn":"我刚走出大楼天就下雨了。  "},{"eng-help":"Let's find a place to shelter from the rain（躲雨） or（否则） we'll get wet.  ","eng":"Let's find a place to shelter from the rain or we'll get wet.  ","zh-cn":"我们找个地方躲雨吧，不然就淋湿了。  "},{"eng-help":"Don't believe whatever（无论什么） he tells you.  ","eng":"Don't believe whatever he tells you.  ","zh-cn":"无论他告诉你什么都不要相信。  "},{"eng-help":"Can I download（下载） this material without paying any money（不付钱）?  ","eng":"Can I download this material without paying any money?  ","zh-cn":"我可以免费下载这个材料吗？  "},{"eng-help":"I was born in the city which you visited last year.  ","eng":"I was born in the city which you visited last year.  ","zh-cn":"我就出生在你去年参观过的那个城市。  "},{"eng-help":"I could hardly(不能) agree to what you said just now（刚才）.  ","eng":"I could hardly agree to what you said just now.  ","zh-cn":"我不能同意你刚才说的话。  "},{"eng-help":"The doctor performed an operation（做手术） on him as soon as he was sent to the hospital（被送到医院）.   ","eng":"The doctor performed an operation on him as soon as he was sent to the hospital.   ","zh-cn":"他被送到医院后，医生立刻给他做了手术。  "},{"eng-help":"Every student has free access to（自由使用） all the sources（资源） in the library.  ","eng":"Every student has free access to all the sources in the library.  ","zh-cn":"每一位学生都可以自由利用图书馆的一切资源。  "},{"eng-help":"He is the only（唯一） Chinese who has ever won the award（获奖）.  ","eng":"He is the only Chinese who has ever won the award.  ","zh-cn":"他是唯一获得过这个荣誉的中国人。  "},{"eng-help":"Li Ming is a friend of mine（我的一位朋友） whom I have known since childhood（从小）.  ","eng":"Li Ming is a friend of mine whom I have known since childhood.  ","zh-cn":"李明是我从小就认识的一位朋友。  "},{"eng-help":"Your composition （作文） is well written, but there's still some room（空间，余地） for improvement（提高，改善）.   ","eng":"Your composition is well written, but there's still some room for improvement.   ","zh-cn":"你的作文很不错，但是还有改进的余地。  "},{"eng-help":"That team withdrew（退出） from the match as a protest（抗议） against the unfair referee（不公平的裁判）.   ","eng":"That team withdrew from the match as a protest against the unfair referee.   ","zh-cn":"那个球队退出了比赛，抗议那个不公平的裁判。  "},{"eng-help":"In the past few days, news of victory has kept coming in（不断传来）.  ","eng":"In the past few days, news of victory has kept coming in.  ","zh-cn":"在过去的几天里胜利的消息不断传来。  "},{"eng-help":"Is this the magazine（杂志） which you want to borrow（借）?  ","eng":"Is this the magazine which you want to borrow?  ","zh-cn":"这就是你要借的那本杂志吗？  "},{"eng-help":"It has not been decided yet（还未决定） whether（是否） they will take part in（参加） our experiment（实验）.  ","eng":"It has not been decided yet whether they will take part in our experiment.  ","zh-cn":"他们是否参加我们的试验还没有决定。  "},{"eng-help":"My idea is that this experiment should be stopped.  ","eng":"My idea is that this experiment should be stopped.  ","zh-cn":"我的意见是这个实验应该停止。  "},{"eng-help":"No matter how much（无论多少） it cost（花费）, he had to get the car fixed（修理）.  ","eng":"No matter how much it cost, he had to get the car fixed.  ","zh-cn":"无论费用多高，他也不得不去修车。  "},{"eng-help":"Only those who work hard will be in a better position（更好的位置） in the competition（竞争）.  ","eng":"Only those who work hard will be in a better position in the competition.  ","zh-cn":"只有勤奋者才会在这场竞争中处于更为有利的地位。  "},{"eng-help":"It is well known（众所周知） that coal（煤炭） is an important source of energy（能源）.  ","eng":"It is well known that coal is an important source of energy.  ","zh-cn":"众所周知，煤炭是重要的能源。  "},{"eng-help":"Scientists think that there is no life on the Mars（火星）.  ","eng":"Scientists think that there is no life on the Mars.  ","zh-cn":"科学家们认为火星上没有生命。  "},{"eng-help":"He tried many times to pass the examination, and in the end he succeeded.","eng":"He tried many times to pass the examination, and in the end he succeeded.","zh-cn":"他多次努力想通过考试,最后成功了。"},{"eng-help":"Don’t leave（留下） your little sister alone（单独的） at home","eng":"Don’t leave your little sister alone at home","zh-cn":"不要把你的小妹妹独自留在家里"},{"eng-help":"She has only a small room to live in.","eng":"She has only a small room to live in.","zh-cn":"他仅住在一间小小的房间里。"},{"eng-help":"In fact,he did not even（甚至） own（拥有） a bed.","eng":"In fact,he did not even own a bed.","zh-cn":"实际上，他连张床都没有。"},{"eng-help":"I can't remember where I left（遗留，落下） my English book.","eng":"I can't remember where I left my English book.","zh-cn":"我想不起我把英语书放在哪儿了。"},{"eng-help":"I was wondering if you could help me to find this book.","eng":"I was wondering if you could help me to find this book.","zh-cn":"我想知道你能否帮我找到这本书。"},{"eng-help":"I'll go with you and make sure（确信） you understand it.","eng":"I'll go with you and make sure you understand it.","zh-cn":"我随你去并确信你理解会用才行。"},{"eng-help":"Why not go out for walk?","eng":"Why not go out for walk?","zh-cn":"为什么不出去散步呢？ "},{"eng-help":"I've never heard such good music in game.","eng":"I've never heard such good music in game.","zh-cn":"我在游戏里从没有听过这么好的音乐。"},{"eng-help":"you are free（自由的） to go or stay.","eng":"you are free to go or stay.","zh-cn":"去留自愿。"},{"eng-help":"The best time to read English is in the morning.","eng":"The best time to read English is in the morning.","zh-cn":"阅读英语的最佳时间是早晨。"},{"eng-help":"When she was eighteen, she began to work.","eng":"When she was eighteen, she began to work.","zh-cn":"当她十八岁的时候，她开始工作。"},{"eng-help":"He is leaving China for Britain(英国) tomorrow.","eng":"He is leaving China for Britain tomorrow.","zh-cn":"明天他要离开中国去英国。"},{"eng-help":"Although very important,intelligence（聪明） is not necessarily the key to success.","eng":"Although very important,intelligence is not necessarily the key to success.","zh-cn":"虽然聪明很重要,但未必是成功的关键。"},{"eng-help":"He'll come if it doesn't rain tomorrow.","eng":"He'll come if it doesn't rain tomorrow.","zh-cn":"如果明天不下雨他就来。"},{"eng-help":"Failure is the mother of success.","eng":"Failure is the mother of success.","zh-cn":"失败乃成功之母。"},{"eng-help":"I have something important to tell you.","eng":"I have something important to tell you.","zh-cn":"我有些重要事情要告诉你。"},{"eng-help":"As was expected（正如预料）,he passed the exam easily.","eng":"As was expected,he passed the exam easily.","zh-cn":"果不出所料，他轻而易举地通过了考试。"},{"eng-help":"Bob was such a hardworking man that he always worked late till night.","eng":"Bob was such a hardworking man that he always worked late till night.","zh-cn":"鲍勃是一个如此勤奋的人，以至于他总是工作到深夜。"},{"eng-help":"Because of his carelessness(粗心),Jack hit his car into(撞上) a big tree by the roadside. ","eng":"Because of his carelessness,Jack hit his car into a big tree by the roadside. ","zh-cn":"因为粗心，杰克开车撞倒路边的树上了。"},{"eng-help":"In many factories,an assembly line（流水线） is used to speed up（加速） the efficiency（效率） of the workforce. ","eng":"In many factories,an assembly line is used to speed up the efficiency of the workforce. ","zh-cn":"许多工厂采用流水线来提高工作效率。"},{"eng-help":"In no other region（地区） in the world can you find such rare（罕见的） animals as giant pandas (大熊猫).","eng":"In no other region in the world can you find such rare animals as giant pandas.","zh-cn":"在世界上的其他任何地区，你都找不到像大熊猫这样的珍稀动物。"},{"eng-help":"Jane has told me a lot about her trip. ","eng":"Jane has told me a lot about her trip. ","zh-cn":"珍妮告诉了我很多关于她旅行的事。"}]

